<template>
    <div class="dialog-box">
        <dy-dialog :appendToBody="false" class="dialog-tree dy-ui" :showClose="false" title="设置"
            dialogHeadColor="rgba(0,0,0,0.9);" dialogBorder="0" dialogBgColor="rgba(0.2,0.2,0.2,0.7);"
            :visible.sync="show">
            <div class="dialog-body">
                <span>环境光强度</span><br>
                <el-slider input-size="mini" v-model="amlightintens" @change="setAmlight"></el-slider>
                <span class="demonstration">直射光强度</span>
                <el-slider v-model="dirlightintens" @change="setDirlight"></el-slider>
                <span class="demonstration">点光源强度</span>
                <el-slider v-model="ptlightintens" @change="setPotlight"></el-slider>
                <span class="demonstration">镜头光强度</span>
                <el-slider v-model="camlightintens" @change="setCamlight"></el-slider>


                <span class="demonstration">切换视角</span>

                <el-select v-model="viewvalue" placeholder="请选择视角" @change="gotoViewbyName" size="mini">
                    <el-option v-for="item in viewoptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select><br>
                <span class="demonstration">正交相机 </span>
                <el-checkbox v-model="OrthCamchecked" @change="setToOrthographicCamera"></el-checkbox><br>
                <span class="demonstration">透明模式 </span>
                <el-checkbox v-model="Transpchecked" @change="setTranspMode"></el-checkbox><br>
                <span class="demonstration">线框模式 </span>
                <el-checkbox v-model="WFchecked" @change="setWireFrameMode"></el-checkbox><br>
                <span class="demonstration">渲染优化 </span>
                <el-checkbox v-model="RRchecked" @change="setRenderReduce">(相机移动时少渲染部分构件以提升性能)</el-checkbox><br>
                <span class="demonstration">背景模式</span>
                <el-select v-model="backmode" @change="setbackground" size="mini">
                    <el-option v-for="item in backoptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select><br>
                <div v-show="colormode"><span class="demonstration">背景颜色</span>
                    <el-input type="color" v-model="backcolor" @change="backcolorchanged"></el-input>
                </div>
            </div>
            <div slot="footer" class="dialog-footer">
            </div>
        </dy-dialog>
    </div>
</template>

<script>
    import {
        mapState
    } from "vuex";
    export default {
        data() {
            return {
                show: false,
                amlightintens: 85,
                dirlightintens: 80,
                ptlightintens: 30,
                camlightintens: 30,
                viewvalue: "",
                OrthCamchecked: false,
                Transpchecked: false,
                WFchecked: false,
                RRchecked: false,
                backmode: null,
                backcolor: "#ffffff",
                backoptions: [{
                    value: null,
                    label: '渐变模式'
                }, {
                    value: 'color',
                    label: '颜色模式'
                }, {
                    value: 'cloudy',
                    label: '天空盒'
                }],
                viewoptions: [{
                    value: 'Top',
                    label: '上'
                }, {
                    value: 'Bottom',
                    label: '下'
                }, {
                    value: 'Left',
                    label: '左'
                }, {
                    value: 'Right',
                    label: '右'
                }, {
                    value: 'Front',
                    label: '前'
                }, {
                    value: 'Back',
                    label: '后'
                }],
            }
        },
        components: {

        },

        computed: {
            colormode() {
                return this.backmode == "color"
            },

            ...mapState({
                dgp: state => state.dgp.dgp, //dgp对象
                Svg_Src: state => state.dgp.SvgSrc, //图纸
            }),

            //设置弹窗显示

        },
        watch: {
        },
        created() {

        },
        mounted() {

        },

        methods: {
            close() {
                this.$store.dispatch('dgp/update_settingDialogShow', false)
            },
            setRenderReduce() {
                this.$parent.setRenderReduce(this.RRchecked)
            },
            setAmlight(n) {
                this.$parent.setAmlight(n / 40)
            },
            setDirlight(n) {
                this.$parent.setDirlight(n / 40)
            },
            setPotlight(n) {
                this.$parent.setPotlight(n / 8)
            },
            setCamlight(n) {
                this.$parent.setCamlight(n / 8)
            },
            gotoViewbyName(s) {
                this.$parent.gotoViewbyName(s)
            },
            setToOrthographicCamera(e) {
                this.$parent.setToOrthographicCamera(e)
            },
            setTranspMode(e) {
                this.$parent.setTranspMode(e)
            },
            setWireFrameMode(e) {
                this.$parent.setWireframeMode(e)
            },
            setbackground(e) {
                if (e == "color")
                    this.$parent.setbackground(this.backcolor)
                else
                    this.$parent.setbackground(e)
            },
            backcolorchanged(e) {
                this.$parent.setbackground(e)
            },
            setlight(l1, l2, l3) {
                this.amlightintens = l1
                this.dirlightintens = l2
                this.hemlightintens = l3
                this.setAmlight(l1)
                this.setDirlight(l2)
                this.setHemlight(l3)
            },

            lighta() {
                this.setlight(100, 100, 100)
            },
            lightb() {
                this.setlight(70, 70, 70)
            },
            lightc() {
                this.setlight(50, 50, 50)
            }

        },

        beforeDestroy() { },
    }
</script>
<style scoped src="@/assets/three/custom2.css"></style>
<style scoped>
    .main-box {
        margin-top: 20px;
    }

    .el-input {
        width: 70px;
    }
</style>