<template>
    <dy-sidebar-group minusHeight="70px" space="10px" borderRadius="2px" borderWidth="0" backgroundColor="rgba(5, 13, 24, 0.75)" :heightType="4">
        <dy-sidebar-tittle3 titleMargin="5px" :title_CN="'重要报警信息'" :show_EN="false" />
        <div class="weather_around" ref="weather_around">
            <div v-if="alarmArr.length==0" style="text-align: center;margin-top: -25px;">
                <!-- 列表空态样式 -->
                <img src="@/components/opsViewer/img/opsHomeEnv/state_abnormal.svg" alt="">
                <div style="color: #99A9BF;font-size: 12px;margin-top: -20px;">
                    <p>重要报警信息持续监控中...</p>
                    <p>暂无报警信息</p>
                </div>
            </div>
            <div v-else>
                <div v-if="alarmArr.length<=3">
                    <div v-for="(item,index) in alarmArr" :key="index">
                        <div class="alarm" @click="lookatComp(item)">
                            <div class="alarm_title">
                                <img src="@/components/opsViewer/img/opsHomeEnv/alarm_position_i.png" width="12px" height="12px">
                                <span class="alarm_title_txt">
                                    {{item.alarmType==1?'设备':item.alarmType==2?'消防':item.alarmType==3?'安防':'自定义'}}
                                </span>
                            </div>
                            <div class="alarm-text">{{item.eqNumber + item.errorContent}} </div>
                            <p class="alarm-date">{{HAlarmImportant.dataEnabled?currentTime:item.updatedDate}}</p>
                        </div>
                    </div>
                </div>
                <div v-else @mouseenter="rollStop" @mouseleave="rollStart">
                    <swiper ref="mySwiper" id="swiper-container" :options="swiperOptions">
                        <swiper-slide v-for="(item,index) in alarmArr" :key="index">
                            <div class="alarm" @click="lookatComp(item)">
                                <div class="alarm_title">
                                    <img src="@/components/opsViewer/img/opsHomeEnv/alarm_position_i.png" width="12px" height="12px">
                                    <span class="alarm_title_txt">
                                        {{item.alarmType==1?'设备':item.alarmType==2?'消防':item.alarmType==3?'安防':'自定义'}}
                                    </span>
                                </div>
                                <div class="alarm-text">{{item.eqNumber + item.errorContent}} </div>
                                <p class="alarm-date">{{HAlarmImportant.dataEnabled?currentTime:item.updatedDate}}</p>
                            </div>
                        </swiper-slide>
                    </swiper>
                </div>
            </div>
    
        </div>
    </dy-sidebar-group>
    </template>
    
    <script>
    import {
        Swiper,
        SwiperSlide,
    } from 'vue-awesome-swiper'
    import 'swiper/swiper-bundle.css'
    export default {
        components: {
            Swiper,
            SwiperSlide
        },
        name: "side_bar_water_supply",
        data() {
            return {
                currentTime: '',
                swiperOptions: {
                    delay: 3000,
                    speed: 700, // 轮播速度
                    height: 140,
                    slidesPerView: 4,
                    spaceBetween: 3,
                    loop: true,
                    autoplay: true, // 自动轮播
                    direction: 'vertical',
                    pauseOnMouseEnter: true,
                    watchOverflow: true, //当没有足够的slide切换时，例如只有1个slide（非loop），swiper会失效且隐藏导航等。默认不开启这个功能。
                    // Some Swiper option/callback...
                },
                alarmArr: [],
    
            }
        },
        mounted() {
            // this.getOpsAlarmImportant()
            // this.getCurrentTime()
    
        },
        computed: {
            

            
        },
        watch: {
            
        },
        methods: {
    
            judgeData() {
                if (this.HAlarmImportant.dataEnabled) {
                    this.alarmArr = this.HAlarmImportant.alarmImportant
                } else {
                    this.alarmArr = this.alarmImportant
                }
            },
            //开始滚动
            rollStart() {
                if (this.alarmArr.length != 0) {
                    this.$refs.mySwiper.$swiper.autoplay.start()
                }
            },
            // 停止滚动
            rollStop() {
                if (this.alarmArr.length != 0) {
                    this.$refs.mySwiper.$swiper.autoplay.stop()
                }
            },
            //ops-首页-重要报警信息
            getOpsAlarmImportant() {
                this.$store.dispatch("getOpsAlarmImportant").then(res => {
                    // console.log('重要报警信息99999999',res.data.res);
                    this.$store.commit('update_alarmImportant', res.data.res)
    
                }).catch(err => {
                    this.$message.error('网络错误！')
                })
            },
            getCurrentTime() {
    
                var nowdate = new Date();
    
                var month = nowdate.getMonth() + 1,
    
                    date = nowdate.getDate(),
    
                    h = nowdate.getHours(),
    
                    m = nowdate.getMinutes()
    
                this.currentTime = month + "-" + date + " " + h + ":" + m;
    
            },
            //聚焦报警
            lookatComp(item) {
                if (item.elementId) {
                    this.qmodel.lookat_comp(item.elementId) //聚焦选中的构件
                }
    
            },
            //创建标签
            createAlarmTags(list) {
                list.forEach(item => {
                    this.qmodel.insert_2d_Mark(item.elementId[0],
                        `<div style="position:absolute;z-index: 2001;width: 200px;display: flex;bottom:30px;left:-100px; flex-direction: column;">
                <div
                    style="display:flex;flex-direction:column;background: rgba(3,16,22,0.75);border: 1px solid #FF4949;border-radius: 4px;overflow: hidden;">
                    <div
                        style="background:#FF4949;padding: 5px 10px;display: flex;justify-content: start;align-items: center;height:24px;">
                        <p style="padding:0;margin:0;color:#fff;font-size:12px;">${item.eqName}</p>
                    </div>
                    <div style="padding: 10px;">
                        <p style="padding:0;margin:0;color:#fff;margin-left:10px;font-size:12px;"><span
                                style="color:#FF4949">设备编码：</span>${item.eqNumber}
                        </p>
                        <p style="padding:0;margin:0;color:#fff;margin-left:10px;font-size:12px;"><span
                                style="color:#FF4949">报警内容：</span>${item.errorContent}
                        </p>
                        <p style="padding:0;margin:0;color:#fff;margin-left:10px;font-size:12px;"><span
                                style="color:#FF4949">报警时间：</span>${item.updatedDate}
                        </p>
                    </div>
                </div>
    
                <div style="display: flex;justify-content: center;">
                    <svg width="14px" height="7px" viewBox="0 0 14 7">
                        <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                            <g transform="translate(-1515.000000, -608.000000)" fill="#FF4949">
                                <polygon
                                    transform="translate(1522.000000, 608.000000) rotate(45.000000) translate(-1522.000000, -608.000000) "
                                    points="1527 603 1527 613 1517 613"></polygon>
                            </g>
                        </g>
                    </svg>
                </div>
            </div>`, `alarm^${item.elementId[0]}`, null, 2011, true)
                    this.qmodel.insert_2d_Mark(item.elementId[0],
                        `<svg width="32px" height="32px" viewBox="0 0 32 32" version="1.1" >
        <defs>
            <path d="M14.5281899,8 L8,24 L10.7537092,24 L12.3442136,19.8767507 L19.6320475,19.8767507 L21.2225519,24 L24,24 L17.4718101,8 L14.5281899,8 Z M13.1275964,17.859944 L15.9525223,10.6218487 L16.0474777,10.6218487 L18.8486647,17.859944 L13.1275964,17.859944 Z" id="path-682huylawf-1"></path>
        </defs>
        <g id="页面-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
            <g id="标签样式" transform="translate(-352.000000, -428.000000)">
                <g id="编组-47" transform="translate(352.000000, 428.000000)">
                    <circle  stroke-opacity="0.717349179" stroke="#FC4C4C" fill-opacity="0.434178492" fill="#FC4C4C" transform="translate(16.000000, 16.000000) rotate(-270.000000) translate(-16.000000, -16.000000) " cx="16" cy="16" r="15.5"></circle>
                    <g id="形状" fill-rule="nonzero" fill="#FFFFFF">
                        <use xlink:href="#path-682huylawf-1"></use>
                        <use xlink:href="#path-682huylawf-1"></use>
                    </g>
                </g>
            </g>
        </g>
    </svg>`,
                        item.elementId[0], null, 2010)
                })
            },
        },
    
        beforeDestroy() {
    
        }
    }
    </script>
    
    <style scoped>
    .weather_around {
        flex: 1;
    }
    
    .around {
        width: 320px;
    }
    
    .alarm {
        width: 300px;
        height: 30px;
        margin: 0 auto;
        background: #99A9BF20;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 5px;
        cursor: pointer;
    }
    
    .alarm_title {
        height: 18px;
        background: rgba(199, 79, 79, 0.6);
        border-radius: 2px;
        border: 1px solid #ff1616;
        font-size: 10px;
        color: #fff;
        line-height: 18px;
        display: flex;
        align-items: center;
        min-width: 60px;
        justify-content: center;
        transform: scale(0.9);
        margin-left: 5px;
    }
    
    .alarm_title_txt {
        text-align: center;
    }
    
    .alarm-text {
        margin-left: 10px;
        width: 140px;
        color: #fff;
        font-size: 13px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: 13px;
    }
    
    .alarm-date {
        color: #A2BBBC;
        font-size: 12px;
        white-space: nowrap;
        line-height: 13px;
    }
    
    .alarm_title img {
        margin-right: 3px;
        margin-left: 3px;
    }
    
    .alarm_position {
        width: 24px;
        height: 25px;
    }
    
    .alarm_position img {
        width: 24px;
        height: 25px;
    }
    </style>
    