  private initScene() {
    this.scene = new THREE.Scene();
    //this.scene.background = new THREE.Color(0xebf2f7)

    // Find more performance tips at: https://discoverthreejs.com/tips-and-tricks/
    // For performance. call .updateMatrix() manually when needed.
    this.scene.matrixAutoUpdate = matrixAutoUpdate;
    // When testing the performance of your apps, one of the first things you’ll need to do is check whether it is CPU bound, or GPU bound.
    // If performance increases, then your app is GPU bound. If performance doesn’t increase, your app is CPU bound.
    // this.scene.overrideMaterial = new MeshBasicMaterial({ color: 'green' ,transparent:true ,opacity:0.5 })
    let markdiv = document.getElementById(`controltargetmark${this.domid}`)

    this.controltargetmark = new CSS2DObject(markdiv)
    this.controltargetmark.name="controltargetmark"
    this.scene.add(this.controltargetmark)
    this.onControlsanimeend()
  }

  private initRenderer() {
    this.renderer = new WebGLRenderer({ antialias: true, alpha: true });
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(this.width, this.height);
    //this.renderer.outputEncoding = THREE.sRGBEncoding;
    this.renderer.toneMappingExposure = 0.7;
    this.renderer.physicallyCorrectLights = true;
    // const pmremGenerator = new THREE.PMREMGenerator(this.renderer)
    // pmremGenerator.compileEquirectangularShader()
    //this.renderer.setClearColor(0xa9a9a9, 1);
    this.renderer.setClearAlpha(0.1);
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    //this.renderer.domElement.style.zIndex = "6";

    // css2dRenderer.domElement should be added to dom element by caller
    this.css2dRenderer = new CSS2DRenderer();
    this.css2dRenderer.setSize(this.width, this.height);
    this.css2dRenderer.domElement.style.position = 'absolute';
    //this.css2dRenderer.domElement.style.zIndex = "8";
    this.css2dRenderer.domElement.style.top = "0";
    this.css2dRenderer.domElement.style.pointerEvents = "none"

    this.stats = Stats();
  }

  //设置相机
  private initCamera() {
    if (!this.scene) {
      return; // have to init scene first
    }
    // to avoid z-fighting issue, do not set near value too small!
    // https://www.cnblogs.com/lst619247/p/9098845.html
    this.perspectiveCamera = new THREE.PerspectiveCamera(45, this.width / this.height, 20, 100*1000*1000);
    this.perspectiveCamera.position.set(30, 100, 80);
    this.scene.add(this.perspectiveCamera); // need to init scene before camera
    this.camera = this.perspectiveCamera;

    const light = new THREE.PointLight(0xffffff, 3, 180);
    //light.castShadow = true
    this.camera.add(light)
    this.camlight = light
  }

  private setSyncControl(Viewer: Viewer3D) {
    if (Viewer)
      this.controls.setSyncControl(Viewer.controls)
    else
      this.controls.setSyncControl(null)
  }

  //按键控制
  private initControls(isOrthCamera: boolean = false) {
    if (!this.renderer) {
      return;
    }
    // TODO: support touch device later, see https://github.com/tdushinwa/threejs_touchtest/blob/master/js/TrackballControls.js
    //DeviceUtils.printDeviceInfo();

    const camera = isOrthCamera ? this.orthoCamera : this.perspectiveCamera;
    if (!camera) {
      return;
    }
    const controls = new CameraControls(camera, this.renderer.domElement);
    controls.mouseButtons.middle = CameraControls.ACTION.TRUCK
    if (isOrthCamera) {
      this.orthoCameraConrols = controls;
    } else {
      this.perspectiveCameraControls = controls;
    }
    this.controls = controls;
    controls.dollySpeed = 0.5
    controls.maxDistance = 1000
    controls.infinityDolly = true
    controls.minDistance = 2
    //this.controls.update(0.1)
    this.addEvent(controls, "update", this.onControlsChange(this));

    this.addEvent(window, "keydown", this.onKeyDown(this));//监听键盘前后方法

  }
