  public setflycontrol(on: boolean = true) {
    this.controls.dispose()
    if (on) {

      var controls = new FlyControls(this.camera, this.renderer.domElement);

      controls.movementSpeed = 25;
      controls.domElement = this.renderer.domElement;
      controls.rollSpeed = Math.PI / 12;
      controls.autoForward = false;
      controls.dragToLook = true;
      this.perspectiveCameraControls = controls;
      //this.perspectiveCameraControls = controls;

      this.controls = controls
      this.addEvent(controls, "change", this.onControlsChange(this));
    }
    else
      this.initControls()
  }

