  // resize render area
  // if no width or height passed in, use window.innerWidth/window.innerHeight instead
  public resize(width?: number, height?: number) {
    // handle window resize event
    const camera = this.camera;
    if (camera) {
      this.width = width || window.innerWidth;
      this.height = height || window.innerHeight;
      if (camera instanceof THREE.PerspectiveCamera) {
        camera.aspect = this.width / this.height;
      } else if (camera instanceof THREE.OrthographicCamera) {
        camera.left = -this.width / 2;
        camera.right = this.width / 2;
        camera.top = this.height / 2;
        camera.bottom = -this.height / 2;
      }
      camera.updateProjectionMatrix();
      if (this.renderer) {
        this.renderer.setSize(this.width, this.height);
      }
      if (this.css2dRenderer) {
        this.css2dRenderer.setSize(this.width, this.height);
      }
      if (this.composer) {
        this.composer.setSize(this.width, this.height);
      }
      if (this.effectFxaaPass) {
        // eslint-disable-next-line
        this.effectFxaaPass.uniforms["resolution"].value.set(1 / this.width, 1 / this.height)
      }
    }
    this.enableRender();
  }

  public shot() {
    this.renderer.render(this.scene, this.camera);
    return this.renderer.domElement.toDataURL("image/jpeg")
  }
  public setbackground(s: string) {
    if (s == null)
      this.scene.background = null
    else if (s.startsWith("#"))
      this.scene.background = new THREE.Color(s)
    else
      this.createSkyFromTextures(s).then(texture => {
        this.scene.background = texture;
      });
    this.enableRender()

  }

  public async createSkyFromTextures(subFolder: string = "cloudy"): Promise<THREE.CubeTexture> {
    const loader = new THREE.CubeTextureLoader();
    loader.setPath(`skybox/${subFolder}/`);
    // six pictures in order of: x, -x, y, -y, z, -z, aka, right, left, top, bottom, front, back
    const pictures = ["right.jpg", "left.jpg", "top.jpg", "bottom.jpg", "front.jpg", "back.jpg"];
    return new Promise<THREE.CubeTexture>((resolve, reject) => {
      loader.load(pictures, (t: THREE.CubeTexture) => resolve(t));
    });
  }
