  public set_all_comps_visibility(show: boolean){
    this.clearSelection()
    this.resetflash()
    this.scene.traverse(child => {

      if (child instanceof THREE.InstancedMesh) {
        if (child.name == "trangins") {
          child.instanceMatrix.array.set(show ? child.userData.hideMatrix : new Float32Array(child.count * 16), 0)
          child.instanceMatrix.needsUpdate = true
        }
        else if (!child.userData.istranspmesh)
          for (var i = 0; i < child.count; i++) {
            this.setCompVisibility(child, show, i)
          }
      }
      else if (child instanceof THREE.Mesh && !(child.material instanceof MeshBasicMaterial)) {
        child.visible = show
      }
    })
    this.enableRender()
  }
