  public setWireframeMode(on: boolean = true) {
    this.scene.overrideMaterial = null
    on ? ObjectUtils.setWireframeMode(this.scene) : ObjectUtils.revertWireframeMode(this.scene)
    this.enableRender()
  }
  public set_tree_visibility(ids: string, show: boolean) {
    if (!this.loadedModels.hasOwnProperty(ids))
      return;
    for (var i = 0; i < this.scene.children.length; i++) {
      if (this.loadedModels[ids].uuid == this.scene.children[i].uuid) {
        this.scene.children[i].visible = show;
        this.enableRender();
        break;
      }
    }
  }

