  /**
   * Clears the current selection
   */
  public clearSelection() {
    this.selectObject(); // simply select nothing
    this.enableRender();
  }

  public get_cmps_box3(ids: string | string[]): THREE.Box3 {
    if (Array.isArray(ids)) {
      var viewer = this;
      const bbox = new THREE.Box3();
      ids.forEach(id => {
        const object = viewer.UId2Mesh.get(id)
        if (object.mesh instanceof InstancedMesh) {
          const box = SceneUtils.getInstancedMeshBoundingBox(object.mesh, object.id); // use getBoundingBox rather than expandByObject to work for InstancedMesh
          if (!box.isEmpty()) {
            bbox.union(box);
          }
        }
        else if (object.mesh instanceof Mesh) {
          const box = SceneUtils.getBoundingBox(object.mesh); // use getBoundingBox rather than expandByObject to work for InstancedMesh
          if (!box.isEmpty()) {
            bbox.union(box);
          }
        }
      });
      return bbox;
    }
    return this.get_cmps_box3([ids])
  }

  public get_cmp_box3(ids: string) {
    return this.get_cmps_box3([ids]);
  }

  /**
   * insert_2d_Mark
   * ids: 构件uniqueid
   * innerhtml: htmltext
   * id： 标签id控制显隐
   */
  public insert_2d_Mark(ids: string | string[] | THREE.Vector3, innerhtml: string, id: string) {
    let e = this.CssD2dom.get(id)
    if (e) {
      e.element.innerHTML = innerhtml
      e.visible = true
      if (ids instanceof THREE.Vector3)
        e.position.copy(ids)
      else
        this.get_cmps_box3(ids).getCenter(e.position)
      return
    }
    let dom = document.createElement('div')
    dom.innerHTML = innerhtml
    dom.style.pointerEvents="all"
    let camera=this.cesium.viewer.camera
    dom.onclick=function(){
      var center=new Cesium.Cartesian3(-2251255.9512910172,  5019027.859595155,  3218825.5849097497)
      camera.flyTo({
        destination : center,
        orientation : {
          heading : Cesium.Math.toRadians(10),
          pitch : Cesium.Math.toRadians(-45),
          roll : Cesium.Math.toRadians(0)
        },
        duration: 1.5
      });
    }
    this.insert_2d_Dom(ids, dom, id)
  }

  /**
   * insert_2d_Dom
   * ids: 构件uniqueid
   * dom: 直接传HTMLElement，没有用iframe所以可直接传对象
   * id： 标签id控制显隐
   */
  public insert_2d_Dom(ids: string | string[] | THREE.Vector3, dom: HTMLElement, id: string) {
    const obj2d = new CSS2DObject(dom);
    if (ids instanceof THREE.Vector3) {
      obj2d.position.copy(ids)
    }
    else
      this.get_cmps_box3(ids).getCenter(obj2d.position)
    this.scene && this.scene.add(obj2d)
    this.CssD2dom.set(id, obj2d)
  }
  public show_Css2dMark(id: string | string[], show: boolean) {
    if (Array.isArray(id)) {
      id.forEach(i => {
        let d = this.CssD2dom.get(i)
        if (d)
          d.visible = show
      })
    }

    else if (id == "all") {
      this.CssD2dom.forEach(e => {
        e.visible = show
      });
    }
    else {
      let d = this.CssD2dom.get(id)
      if (d)
        d.visible = show
    }
  }

  /**
   * Make camera fly to objects
   */
  public flyToObjects(objects: THREE.Object3D[]) {
    if (!objects || objects.length === 0 || !this.camera) {
      return;
    }
    const eye = new THREE.Vector3();
    const look = new THREE.Vector3();
    Viewer3DUtils.getCameraPositionByObjects(objects, this.camera, eye, look);
    this.flyTo(eye, look);
  }

  /**
   * Make camera fly to an object
   */
  public flyToObject(object: THREE.Object3D) {
    this.flyToObjects([object]);
  }

  /**
   * Flies to current selected object if any
   */
  public isfoucs = false;
  public flyToSelectedObject() {
    if (!this.selectedObject) {
      return;
    }
    let obj = this.selectedObject;
    // if part of InstancedMesh is selected, fly to that part rather than fly to the whole InstancedMesh
    if ((obj instanceof THREE.InstancedMesh || obj instanceof THREE.Group) && obj.userData.clonedMesh) {
      obj = obj.userData.clonedMesh;
    }
    this.flyToObject(obj);
    this.isfoucs = true;

  }

