  //设置
  public set_camera_json(jsonStr: string) {
    if (!jsonStr)
      return
    var cameraClone = JSON.parse(jsonStr);
    let pos = new THREE.Vector3(cameraClone.position[0], cameraClone.position[1], cameraClone.position[2])
    let tar = new THREE.Vector3(cameraClone.target[0], cameraClone.target[1], cameraClone.target[2])
    let rot = new THREE.Vector3(cameraClone.focalOffset[0], cameraClone.focalOffset[1], cameraClone.focalOffset[2])
    this.flyTo(pos, tar, rot);
  }

