    //   r && camera.rotation.set(r.x, r.y, r.z);
    //   t && controls.setTarget(t.x, t.y, t.z,false);
    //   //controls.update(0.1);
    //   this.enableRender();
    // };

    // // there are two steps
    // // 1) change camera's lookAt point in x miliseconds
    // // 2) change camera's position in y miliseconds
    // const t = new THREE.Vector3()
    // controls.setOrbitPoint(t); // have to copy one, otherwise TWEEN breaks the passed in object!
    // console.log(t)
    // const tween1 = new TWEEN.Tween(t);
    // tween1.to(lookAt, 300);
    // tween1.easing(TWEEN.Easing.Sinusoidal.InOut);
    // tween1.onUpdate(() => {
    //   update(undefined, t, rotate);
    // });
    // tween1.onComplete(() => {
    //   const p = camera.position.clone();
    //   const tween2 = new TWEEN.Tween(p);
    //   tween2.to(position, 1000);
    //   tween2.easing(TWEEN.Easing.Sinusoidal.InOut);
    //   tween2.onUpdate(() => {
    //     update(p, lookAt);
    //   });
