export enum EventStatus {
  FAILED = 0,
  RUNNING = 1,
  SUCCEEDED = 2
}


class CoordinateAxesViewport {
  hostRenderer?: Viewer3D;
  camera?: THREE.OrthographicCamera;
  scene?: THREE.Scene;
  renderer?: THREE.WebGLRenderer;
  height = 100; // size of render area
  width = 100;

  constructor(width?: number, height?: number) {
    this.width = width || this.width;
    this.height = height || this.height;
    this.init();
  }

  init() {
    this.initRenderer();
    this.initScene();
    this.animate();
  }

  //设置渲染器
  initRenderer() {
    this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    this.renderer.setSize(this.width, this.height);
  }

  //设置视图
  initScene() {
    this.scene = new THREE.Scene();
    // do not set a background color, thus background is gonna be transparent
    //this.scene.background = new THREE.Color(0xffffff)
    this.camera = new THREE.OrthographicCamera(-1, 1, 1, -1, 0.1, 100);
    this.scene.add(this.camera);
  }


  render() {
    if (this.renderer && this.scene && this.camera) {
      this.update();
      this.renderer.render(this.scene, this.camera);
    }
  }

  animate() {
    requestAnimationFrame(this.animate.bind(this));
    // this.render();
  }

  setHostRenderer(renderer: Viewer3D) {
    this.hostRenderer = renderer;
    this.update();
  }

  update() {
    if (!this.hostRenderer || !this.hostRenderer.camera) {
      return;
    }
    const camera = this.hostRenderer.camera;
    if (camera) {
      const target = new THREE.Vector3();
      camera.getWorldDirection(target);
      const up = camera.up;
      this.updateCameraDirection(target, up);
    }
  }

  /**
   * Update axes according to camera direction.
   * Camera's direction is the only input factor for this class. It always look at the origin.
   * @param direction
   */
  updateCameraDirection(direction: THREE.Vector3, up: THREE.Vector3) {
    if (!this.camera || !direction) {
      return;
    }
    direction.normalize();
    const distanceFactor = 2; // keep camera a little farer, so it looks better
    const centerDelta = 0.3; // put the lookAt point to be in the first quadrant
    this.camera.position.set(-direction.x * distanceFactor + centerDelta, -direction.y * distanceFactor + centerDelta, -direction.z * distanceFactor + centerDelta);
    this.camera.lookAt(centerDelta, centerDelta, centerDelta); // it always looks at the origin
    this.camera.up = up;
  }

  dispose() {
    if (!this.scene || !this.camera) {
      return;
    }
    this.scene.clear(); // remove all child objects
    this.hostRenderer = undefined;
    this.camera = undefined;
    this.scene = undefined; // this is necessary to call
  }
}

class DeviceUtils {
  /**
   * Checks if it is opened in touch screen device, like iphone, ipad, etc.
   */
  static isTouchScreenDevice(): boolean {
    // return !!("ontouchstart" in window || navigator.maxTouchPoints);
    return !!("ontouchstart" in window);
  }

  static printDeviceInfo() {
    const ua = navigator.userAgent;
    const isAndroid = /(?:Android)/.test(ua);
    const isFireFox = /(?:Firefox)/.test(ua);
    const isChrome = /(?:Chrome|CriOS)/.test(ua);
    const isTablet = /(?:iPad|PlayBook)/.test(ua) || (isAndroid && !/(?:Mobile)/.test(ua)) || (isFireFox && /(?:Tablet)/.test(ua));
    const isiPhone = /(?:iPhone)/.test(ua) && !isTablet;
    const isPc = !isiPhone && !isAndroid;
    const isTouchDevice = DeviceUtils.isTouchScreenDevice();
    if (isAndroid) console.log("[DI] is android");
    if (isFireFox) console.log("[DI] is fireFox");
    if (isChrome) console.log("[DI] is chrome");
    if (isTablet) console.log("[DI] is tablet");
    if (isiPhone) console.log("[DI] is iPhone");
    if (isPc) console.log("[DI] is PC");
    if (isTouchDevice) console.log("[DI] is touch device");
  }

  /**
   * Gets GPU Graphics card info
   * @param canvas
   */
  static getWebGlRendererInfo(canvas: HTMLCanvasElement): string {
    if (!canvas) {
      throw new Error("Invalid canvas!");
    }
    // Please find more details about getContext() via this link:
    // https://developer.mozilla.org/en-US/docs/Web/API/HTMLCanvasElement/getContext
    const webgl: any = canvas.getContext("webgl2") || canvas.getContext("webgl") || canvas.getContext("experimental-webgl")
    const info = webgl && webgl.getExtension("WEBGL_debug_renderer_info");
    let glRenderer = "unknown";
    if (info) {
      glRenderer = webgl.getParameter(info.UNMASKED_RENDERER_WEBGL);
    }
    return glRenderer;
  }
}
