<template>
    <dy-sidebar-group  space="5px" borderRadius="2px" borderWidth="0" backgroundColor="rgba(5, 13, 24, 0.75)" :heightType="3.5">
        <dy-sidebar-tittle3 titleMargin="5px" :title_CN="'楼栋环境综合参数'" :show_EN="false" />
    
        <div class="weather_around" ref="weather_around">
            <div class="environment">
                <div v-for="(item,index) in envData" :key="index" class="indoor" :style="{'backgroundImage': 'url(' + ( require('@/components/opsViewer/img/'+ item.imgBg +'.svg')) + ')'}">
                    <img class="img_l_t" src="@/components/opsViewer/img/temperature_jiao.svg"><img class="img_l_b" src="@/components/opsViewer/img/temperature_jiao.svg">
                    <img class="img_r_t" src="@/components/opsViewer/img/temperature_jiao.svg"><img class="img_r_b" src="@/components/opsViewer/img/temperature_jiao.svg">
                    <p :class="`indoor-point`">{{item.point}}</p>
                    <div>
                        <p :class="`indoor-value ${item.color}`">{{item.value}}</p>
                        <p :class="`indoor-info`">{{item.info}}</p>
                        <p :class="`indoor-evaluate ${item.color}`">{{item.evaluate}}</p>
                    </div>
                </div>
            </div>
    
        </div>
    
    </dy-sidebar-group>
    </template>
    
    <script>
    
    export default {
        name: "side_bar_building_environment",
        data() {
            return {
                opsHomeEnv: {
                    temperature: 15,
                    co2: 569,
                    co: 0,
                    people: 6,
                    noise: 40,
                    humidity:61,
                }
            }
        },
    
        mounted() {

        },
    
        computed: {
            //环境参数图表
            envData() {
                return [{
                    point: "℃", //单位
                    value: this.opsHomeEnv.temperature?this.opsHomeEnv.temperature:15, //展示值
                    info: "室内平均温度", //说明
                    evaluate: this.opsHomeEnv.temperature <= 20 ? '寒冷' : this.opsHomeEnv.temperature > 20 && this.opsHomeEnv.temperature <= 23 ? '微凉' : this.opsHomeEnv.temperature > 23 && this.opsHomeEnv.temperature <= 25 ? '舒适' : this.opsHomeEnv.temperature > 25 && this.opsHomeEnv.temperature <= 30 ? '温暖' : '炎热', //评价
                    imgBg: this.opsHomeEnv.temperature <= 20 ? 'opsHomeEnv/temperature_1' : this.opsHomeEnv.temperature > 20 && this.opsHomeEnv.temperature <= 23 ? 'opsHomeEnv/temperature_2' : this.opsHomeEnv.temperature > 23 && this.opsHomeEnv.temperature <= 25 ? 'opsHomeEnv/temperature_3' : this.opsHomeEnv.temperature > 25 && this.opsHomeEnv.temperature <= 30 ? 'opsHomeEnv/temperature_4' : 'opsHomeEnv/temperature_5', //背景图标
                    color: this.opsHomeEnv.temperature <= 20 ? 'cold' : this.opsHomeEnv.temperature > 20 && this.opsHomeEnv.temperature <= 23 ? 'alert' : this.opsHomeEnv.temperature > 23 && this.opsHomeEnv.temperature <= 25 ? 'normal' : this.opsHomeEnv.temperature > 25 && this.opsHomeEnv.temperature <= 30 ? 'warm' : 'alarm', //颜色
                }, {
                    point: "PPM", //单位
                    value: this.opsHomeEnv.co2, //展示值
                    info: "室内CO2浓度", //说明
                    evaluate: this.opsHomeEnv.co2 <= 500 ? '优秀' : this.opsHomeEnv.co2 > 500 && this.opsHomeEnv.co2 <= 700 ? '正常' : '超标', //评价
                    imgBg: this.opsHomeEnv.co2 <= 500 ? 'opsHomeEnv/co2_1' : this.opsHomeEnv.co2 > 500 && this.opsHomeEnv.co2 <= 700 ? 'opsHomeEnv/co2_2' : 'opsHomeEnv/co2_3', //背景图标
                    color: this.opsHomeEnv.co2 <= 500 ? 'normal' : this.opsHomeEnv.co2 > 500 && this.opsHomeEnv.co2 <= 700 ? 'alert' : 'alarm', //颜色
                }, {
                    point: "PPM", //单位
                    value: this.opsHomeEnv.co, //展示值
                    info: "停车场CO浓度", //说明
                    evaluate: this.opsHomeEnv.co < 30 ? '优秀' : this.opsHomeEnv.co == 30 ? '正常' : '超标', //评价
                    imgBg: this.opsHomeEnv.co < 30 ? 'opsHomeEnv/co_1' : this.opsHomeEnv.co == 30 ? 'opsHomeEnv/co_2' : 'opsHomeEnv/co_3', //背景图标
                    color: this.opsHomeEnv.co < 30 ? 'normal' : this.opsHomeEnv.co == 30 ? 'alert' : 'alarm', //颜色
                }, {
                    point: "%", //单位
                    value: this.opsHomeEnv.people, //展示值
                    info: "人员密集度", //说明
                    evaluate: this.opsHomeEnv.people <= 30 ? '空旷' : this.opsHomeEnv.people > 30 && this.opsHomeEnv.people <= 70 ? '正常' : '拥挤', //评价
                    imgBg: this.opsHomeEnv.people <= 30 ? 'opsHomeEnv/people_1' : this.opsHomeEnv.people > 30 && this.opsHomeEnv.people <= 70 ? 'opsHomeEnv/people_2' : 'opsHomeEnv/people_3', //背景图标
                    color: this.opsHomeEnv.people <= 30 ? 'normal' : this.opsHomeEnv.people > 30 && this.opsHomeEnv.people <= 70 ? 'alert' : 'alarm', //颜色
                }, {
                    point: "dB", //单位
                    value: this.opsHomeEnv.noise?this.opsHomeEnv.noise:"40", //展示值
                    info: "室内噪音平均值", //说明
                    evaluate: "安静", //评价
                    imgBg: 'opsHomeEnv/noise_1', //背景图标
                    color: 'normal', //颜色
                }, {
                    point: "%", //单位
                    value: this.opsHomeEnv.humidity, //展示值
                    info: "室内平均湿度", //说明
                    evaluate: this.opsHomeEnv.humidity <= 40 ? '干燥' : this.opsHomeEnv.humidity > 40 && this.opsHomeEnv.humidity <= 80 ? '正常' : '潮湿', //评价
                    imgBg: this.opsHomeEnv.humidity <= 40 ? 'opsHomeEnv/humidity_1' : this.opsHomeEnv.humidity > 40 && this.opsHomeEnv.humidity <= 80 ? 'opsHomeEnv/humidity_2' : 'opsHomeEnv/humidity_3', //背景图标
                    color: this.opsHomeEnv.humidity <= 40 ? 'warm' : this.opsHomeEnv.humidity > 40 && this.opsHomeEnv.humidity <= 80 ? 'alert' : 'alarm', //颜色
                }, ]
            },
    
        },
    
        watch: {

        },
    
        methods: {
    

    
        },
        beforeDestroy() {
    
        }
    }
    </script>
    
    <style scoped>
    .weather_around {
        flex: 1;
        display: flex;
        /* overflow: auto; */
        overflow-y: hidden;
    }
    
    .environment {
        width: 315px;
        margin-bottom: 10px;
    }
    
    .indoor {
        float: left;
        position: relative;
        width: 96px;
        height: 50%;
        margin-right: 5px;
        margin-bottom: 5px;
        min-height: 60px;
        max-height: 130px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .indoor-point {
        line-height: 20px;
        font-size: 12px;
        text-align: right;
        color: #A2BBBC;
        position: absolute;
        top: 0;
        right: 10px;
        transform: scale(0.9);
    }
    
    .indoor-info {
        font-size: 12px;
        text-align: center;
        color: #99A9BF;
        transform: scale(0.9);
    }
    
    .indoor-value {
        font-size: 25px;
        font-weight: bold;
        text-align: center;
        line-height: 25px;
    }
    
    .indoor-evaluate {
        font-size: 10px;
        text-align: center;
        transform: scale(0.9);
    }
    
    .indoor>img {
        width: 5px;
        height: 5px;
        position: absolute;
    }
    
    .img_l_t {
        left: 0;
        top: 0;
    }
    
    .img_l_b {
        transform: rotate(-90deg);
        left: 0;
        bottom: 0;
    }
    
    .img_r_t {
        transform: rotate(90deg);
        right: 0;
        top: 0;
    }
    
    .img_r_b {
        transform: rotate(180deg);
        right: 0;
        bottom: 0;
    }
    
    .environment>div:nth-child(1),
    .environment>div:nth-child(4) {
        margin-left: 10px;
    }
    
    .alert {
        color: #00C0FF;
        text-shadow: 0px 0px 8px #00C0FF;
    }
    
    .normal {
        color: #00B0A1;
        text-shadow: 0px 0px 8px #00B0A1;
    }
    
    .alarm {
        color: #FF7A40;
        text-shadow: 0px 0px 8px #FF7A40;
    }
    
    .cold {
        color: #4A6CFF;
        text-shadow: 0px 0px 8px #4A6CFF;
    }
    
    .warm {
        color: #FACE5A;
        text-shadow: 0px 0px 8px #FACE5A;
    }
    </style>
    