  //axios新增修改参数
  function getwt_mesageValue(wt_mesage, ruleForm) {
    const params = new FormData();
    //获取弹窗类型
    // let manytomanyArr = []
    // let manytomanyarrdata = manytomanyarrData.data().manytomanyArr
    // if (ifrrandnotnone(manytomanyarrdata)) {
    //   manytomanyarrdata.forEach(element => {
    //     manytomanyArr.push(element.paramtype)
    //   });
    // }
    //获取弹窗类型
    for (let index = 0; index < wt_mesage.length; index++) {
      let key = wt_mesage[index].prop;
      // console.log(key)
      let paramtype = wt_mesage[index].paramtype;
      if (key && typeof ruleForm[key] != "undefined" && ruleForm[key] != null && wt_mesage[index].ifupload !== false) {
        if (paramtype == "select" && wt_mesage[index].multiple) {
          //下拉多选
          params.append(key, JSON.stringify(ruleForm[key]))
        } else if (paramtype == 'time' || paramtype == 'start_end_daterange') {
          //日期时间
          if (wt_mesage[index].datetype) {
            params.append(key, formatDate(ruleForm[key], 'yyyy-MM-dd HH:mm:ss'));
          } else if (wt_mesage[index].timetype) {
            params.append(key, formatDate(ruleForm[key], 'yyyy-MM-dd HH:mm:ss'));
          } else {
            params.append(key, formatDate(ruleForm[key], 'yyyy-MM-dd HH:mm:ss'));
          }
        } else if (paramtype == 'upload') {
          /*附件*/
          let exisfile = [];
          for (let index = 0; index < ruleForm[key].length; index++) {
            if (typeof ruleForm[key][index].raw == "undefined") {
              //已存在文件
              exisfile.push(ruleForm[key][index]);
            } else {
              //新增文件
              params.append(key + "_files", ruleForm[key][index].raw);
            }
          }
          params.append(key, JSON.stringify(exisfile));
          /*附件*/
        } else if (
          manytomanyArr.some(itk => itk == paramtype)
        ) {
          //manytomanyArr里面类型
          if (ruleForm[key].length > 0) {
            params.append(key, deveridarrry(ruleForm[key]));
          } else {
            params.append(key, '');
          }
        } else if (paramtype == 'keyinobject' || paramtype == 'objectinkey') {
          params.append(key, ruleForm[key].id);
        } else if (paramtype == 'imgupload') {
          //图片
          let pictures = [];
          for (let index = 0; index < ruleForm[key].length; index++) {
            const element = ruleForm[key][index];
            //console.log(element.raw)
            if (typeof element.raw == "undefined") {
              pictures.push(element);
            } else {
              //"imgs"
              params.append(key + "_files", ruleForm[key][index].raw);
            }
          }
          params.append(key, JSON.stringify(pictures));
        } else if (paramtype == 'cascader' && ifrrandnotnone(ruleForm[key])) {
          params.append(key, ruleForm[key][ruleForm[key].length - 1])
        } else {
          params.append(key, ruleForm[key])
        }
      }
    }
    return params;
  }


  //获取queryBar筛选
  function gettableDataqueryselet(searchList, searchBox) {
    let params = {}
    for (let index = 0; index < searchList.length; index++) {
      let ele = searchList[index]
      let key = ele.key;
      // if (key == 'is_active') {
      //   console.log(key, searchBox[key], searchBox[key] !== '')
      // }
      if (typeof searchBox[key] != 'undefined' && searchBox[key] != null && searchBox[key] !== "") {
        if (ele.searchtype == 'daterange') {
          //时间段选择
          let type = (typeof ele.type == 'undefined' ? "yyyy-MM-dd" : 'yyyy-MM-dd HH:mm:ss')
          if (ele.timeType) {
            params[key] = JSON.stringify([formatDate(searchBox[key][0],
              'yyyy-MM-dd 00:00:00'), formatDate(searchBox[key][1],
              'yyyy-MM-dd 23:59:59')])
          } else {
            params[key] = JSON.stringify([formatDate(searchBox[key][0],
              type), formatDate(searchBox[key][1],
              type)])
          }
        } else if (ele.searchtype == 'time') {
          //时间
          params[key] = formatDate(searchBox[key], "yyyy-MM-dd")
        } else if (ele.searchtype == 'sortSelect') {
          //排序筛选
          //   console.log(key, searchBox[key])
          if (ifrrandnotnone(searchBox[key]) && searchBox[key].length == 2) {
            params[key] = {
              key: searchBox[key][0],
              order: searchBox[key][1]
            }
          }
        } else if (['cascader', 'department'].includes(ele.searchtype) && ifrrandnotnone(searchBox[key])) {
          //科室级联筛选
          params[key] = searchBox[key][searchBox[key].length - 1]
        } else if (ele.searchtype == 'select' && ele.multiple !== false && ifrrandnotnone(searchBox[key])) {
          /* 复选框 */
          // console.log(JSON.stringify(searchBox[key]))
          params[key] = JSON.stringify(searchBox[key])
        } else {
          params[key] = searchBox[key];
        }
      }
    }
    return params
  }


