  /* 返回列表页更新的路径 */
  function returnPageList(route) {
    // console.log(formatroute(route))
    let returnpage = null
    let visitedViews = store.state.tagsView.visitedViews
    visitedViews.forEach(element => {
      if (element.path == formatroute(route)) {
        returnpage = {
          path: element.path,
          query: element.query
        }
      }
    });
    if (returnpage == null) {
      returnpage = {
        path: formatroute(route),
      }
    }
    return returnpage
  }

