  //获取权限菜单
  function getasyncRoutesMenu(routek, parent_title = "") {
    const res = []
    for (let index = 0; index < routek.length; index++) {
      const element = routek[index];
      let objck = {
        key: '',
        label: '',
      }
      if (!element.hidden) {
        if (element.meta && element.meta.title) {
          // let key = element.meta.key ? element.meta.key : element.meta.name ? element.meta.name : element.meta.title

          let key = ""
          if (element.meta.key) {
            key = element.meta.key
          } else {
            key = element.path
            if (key.substr(0, 1) == '/') {
              key = key.substr(1, key.length - 1)
            }
          }

          objck.key = parent_title + (parent_title != "" ? "&&" : '') + key
          objck.label = element.meta.title

        }
        if (element.children && !(hasOneShowingChild(element.children, element) && (!onlyOneChild
            .children || onlyOneChild.noShowingChildren) && !element.alwaysShow)) {
          objck.children = getasyncRoutesMenu(element.children, objck.key)
        } else {
          let children = [];
          if (typeof element.operating != "undefined") {
            for (
              let i = 0; i < element.operating.length; i++
            ) {
              const ele = element.operating[i];
              let obpj = {
                key: objck.key + "||" + ele.value,
                label: ele.label,
              }
              if (ele.selet_role) {
                obpj.selet_role = ele.selet_role
                if ((objck.key + "||" + ele.value) == neetrole) {
                  selet_role = ele.selet_role
                  break
                  // console.log(selet_role)
                  //  return selet_role
                }
              }
              children.push(obpj);
            }
            objck.children = children
          }
        }
        res.push(objck)
      }
    }
    //  console.log(selet_role)
    return selet_role
  }

  function hasOneShowingChild(children = [], parent) {
    const showingChildren = children.filter(item => {
      if (item.hidden) {
        return false
      } else {
        // Temp set(will be used if only has one showing child)
        onlyOneChild = item
        return true
      }
    })
    // When there is only one child router, the child router is displayed by default
    if (showingChildren.length === 1) {
      return true
    }

    // Show parent if there are no child router to display
    if (showingChildren.length === 0) {
      onlyOneChild = {
        ...parent,
        path: '',
        noShowingChildren: true
      }
      return true
    }

    return false
  }


  //日期格式转化
  function verdate(timew) {
    let currentdate = "";
    if (timew != null) {
      let char = "-";
      let date = new Date(timew);
      let day = date.getDate();
      let month = date.getMonth() + 1; //注意月份需要+1
      let year = date.getFullYear();
      //补全0，并拼接
      currentdate =
        year +
        char +
        (month < 10 ? "0" + month : month) +
        char +
        (day < 10 ? "0" + day : day);
    }

    return currentdate;
  }
  //获取给定日期day天后的日期
  function getDay(day, datec = new Date()) {
    //Date()返回当日的日期和时间。
    var days = new Date(datec);
    //getTime()返回 1970 年 1 月 1 日至今的毫秒数。
    var gettimes = days.getTime() + 1000 * 60 * 60 * 24 * day;
    //setTime()以毫秒设置 Date 对象。
    days.setTime(gettimes);
    var year = days.getFullYear();
    var month = days.getMonth() + 1;
    if (month < 10) {
      month = "0" + month;
    }
    var today = days.getDate();
    if (today < 10) {
      today = "0" + today;
    }
    return year + "-" + month + "-" + today;
  }

  //发动机参数计算
  function countparameters(dataK) {
    let returnV = "";
    if (dataK != '[{"key":"","val":""}]') {
      // console.log(JSON.parse(dataK))
      try {
        dataK = JSON.parse(dataK);
        for (let index = 0; index < dataK.length; index++) {
          const element = dataK[index];
          if (element.key == "额定功率(KW)" && element.val != "") {
            returnV = returnV + element.val + "KW" + "/";
          } else if (element.key == "额定转速(rpm)" && element.val != "") {
            returnV = returnV + element.val + "rpm" + "/";
          } else if (element.key == "最大扭矩(Nm)" && element.val != "") {
            returnV = returnV + element.val + "Nm" + "/";
          } else if (element.key == "最大扭矩转速(rpm)" && element.val != "") {
            returnV = returnV + element.val + "rpm" + "/";
          }
        }
      } catch (error) {
        returnV = "";
      }
    }
    if (returnV != "") {
      returnV = returnV.substring(0, returnV.length - 1);
    }
    return returnV;
  }

  //获取给定日期month月后的日期
  function getdatamonth(datec = new Date(), month) {
    datec = new Date(datec);
    // 因为getMonth()获取的月份的值只能在0~11之间所以我们在进行setMonth()之前先给其减一
    datec.setMonth(datec.getMonth() - 1 + month);
    var yy1 = datec.getFullYear();
    var mm1 = datec.getMonth() + 1;
    //console.log(mm1);
    var dd1 = datec.getDate();
    if (mm1 < 10) {
      mm1 = "0" + mm1;
    }
    if (dd1 < 10) {
      dd1 = "0" + dd1;
    }
    return yy1 + "-" + mm1 + "-" + dd1;
  }

  //获取当前月有多少天
  function getCountDays(datea) {
    //console.log(this.datem);
    var curDate = new Date(datea);
    /* 获取当前月份 */
    var curMonth = curDate.getMonth();
    /*  生成实际的月份: 由于curMonth会比实际月份小1, 故需加1 */
    curDate.setMonth(curMonth + 1);
    /* 将日期设置为0, 这里为什么要这样设置, 我不知道原因, 这是从网上学来的 */
    curDate.setDate(0);
    //console.log(curDate.getDate())
    /* 返回当月的天数 */
    return curDate.getDate();
  }

  //将普通数组转换为tree数组
  /* 
  a:数组
  idStr:id名称
  pidStr:父id名称
  chindrenStr:children名称
   */
  function transDatatotree(a, idStr, pidStr, chindrenStr) {
    const r = []
    const hash = {}
    const id = idStr
    const pid = pidStr
    const children = chindrenStr
    let i = 0
    let j = 0
    const len = a.length
    for (; i < len; i++) {
      hash[a[i][id]] = a[i]
    }
    for (; j < len; j++) {
      const aVal = a[j]
      const hashVP = hash[aVal[pid]]
      if (hashVP) {
        !hashVP[children] && (hashVP[children] = [])
        hashVP[children].push(aVal)
      } else {
        r.push(aVal)
      }
    }
    return r
  }

  /* 处理科室信息 */
  function dealdepartment(searchList, res, key) {
    /* 把东风商用车筛选掉 */
    if (searchList !== "all") {
      for (let index = res.length - 1; index >= 0; index--) {
        const element = res[index];
        if (element.parent_id == null) {
          res.splice(index, 1)
        }
      }
    }
    let departmentArr = transDatatotree(res, "id", "parent_id", "children") //将普通数组转换为tree数组
    //如果为null
    if (searchList === null || searchList === "all") {
      return departmentArr
    } else {
      let ink = searchList.findIndex(i => i.key === key)
      if (ink == -1) {
        return false
      } else {
        return {
          ink: ink,
          departmentArr: departmentArr
        }
      }
    }
  }

  //日期格式化
  function formatDate(date, fmt) {
    //  console.log(typeof date == 'undefined')
    if (date == '' || date == null || typeof date == 'undefined') {
      fmt = "";
    } else {
      date = new Date(date);
      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          (date.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      }
      let o = {
        "M+": date.getMonth() + 1,
        "d+": date.getDate(),
        "h+": date.getHours() % 12 == 0 ? 12 : date.getHours() % 12, //小时
        "H+": date.getHours(), //小时
        "m+": date.getMinutes(),
        "s+": date.getSeconds()
      };
      for (let k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          let str = o[k] + "";
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length === 1 ? str : padLeftZero(str)
          );
        }
      }
    }
    return fmt;
  }

  /* 路径格式化 */
  function formatroute(path) {
    let comdata = ""
    let arrA = path.split("/")
    let flag = 0
    for (let index = 0; index < arrA.length; index++) {
      if (arrA[index] != '') {
        if (silderlist.includes('/' + arrA[index])) {
          /* 一级目录判断 */
          comdata = arrA[index]
          break
        }
        comdata = comdata + (comdata != '' ? '/' : '') + arrA[index]
        flag++
        if (flag == 2) {
          break
        }
      }
    }
    // console.log(comdata)
    return ("/" + comdata)
  }
  //将日期转换成几号或者几月几号
  function changeformatDate(date) {
    date = String(date)
    let str = ''
    let arr = date.split("-")
    let time = formatDate(new Date(), "yyyy-MM-dd").split("-")
    if ((arr[0] == time[0] && arr[1] == time[1])) {
      str = arr[2] + "号"
    } else {
      str = arr[1] + "月" + arr[2] + "号"
    }
    return str
  }

  function padLeftZero(str) {
    return ("00" + str).substr(str.length);
  }

  //从对象选项中对应label，value
  function matchOption(Option = [], key) {
    let label = "";
    for (let index = 0; index < Option.length; index++) {
      const element = Option[index];
      //console.log(key, element.value, key == element.value)
      if (key == element.value) {
        label = element.label;
      }
    }
    // console.log(label)
    return label;
  }

  //匹配数组中的一个参数，获取另一个参数
  function matchArrParm(Arr = [], paramV1, ParmName1, ParmName2) {
    let paramV2 = null;
    for (let index = 0; index < Arr.length; index++) {
      const element = Arr[index];
      //console.log(key, element.value, key == element.value)
      if (paramV1 == element[ParmName1]) {
