          if (res.length == 1 || res.length == 0) {
            /* 1 有存储列表   0无存储列表 */
            returnarr[0] = res.length == 1 ? res[0] : null
            returnarr[1] = (res.length == 1 && res[0].pagecount) ? res[0].pagecount : 10
          } else if (res.length > 1) {
            console.log('查询到' + res.length + '条拉')
          }
        }
        return returnarr

      },


      /* 
      get方法访问
      api:api接口名称
      params:传递参数
       */
      getapidata(api, params = {}) {
        // store.commit('ifloading', true)
        let allparams = {
          params: params,
          api: api,
        }
        store.dispatch("getmsgData", allparams).then(response => {
            let res = response.data;
            // console.log(res)
            store.commit('ifloading', {
              type: 'success',
              api: api,
              method: 'get',
              res: res
            })
          })
          .catch(err => {
            store.commit('ifloading', "fail")
            console.log(err);
            Vue.prototype.$message.error("网络连接错误" + api);
          });
      },

