      /* 
      获取查询栏科室信息并赋值
       */
      getdepartmentData(searchList, key) {
        return new Promise(resolve => {
          let res = []
          if (ifrrandnotnone(JSON.parse(localStorage.getItem("department")))) {
            res = JSON.parse(localStorage.getItem("department"))
            resolve(dealdepartment(searchList, res, key))
          } else {
            let allparams = {
              params: {},
              api: 'department',
            }
            store.dispatch("getmsgData", allparams).then(response => {
                res = response.data;
                resolve(dealdepartment(searchList, res, key))
              })
              .catch(err => {
                console.log(err);
                Vue.prototype.$message.error("网络连接错误" + 'department');
              });
          }

        })
      },


      /* 
