      creatnewcode(table_type) {
        return new Promise(resolve => {
          let api = "noSeq"
          let allparams = {
            params: {
              table_type: table_type
            },
            api: api,
            method: 'post',
          }
          store.dispatch("editmsgData2", allparams).then(response => {
              let res = response.data;
              if (res.status) {
                // console.log(res.msg)
                resolve(res.msg)
              } else {
                resolve(null)
                Vue.prototype.$message.error(res.msg);
              }
            })
            .catch(err => {
              resolve(null)
              console.log(err);
              Vue.prototype.$message.error("网络连接错误" + api);
            });
        })
      },

      /* 
      保存当前登录用户信息
       */
      getcurrentUserMsg(id) {
        let params = {};
        params.id = id
        let allparams = {
          params: params,
          api: 'users',
        }
        store
          .dispatch("getmsgData", allparams)
          .then(response => {
            let res = response.data;
            // console.log(res.results[0].departmentRoles)
            localStorage.setItem("UserMsg", JSON.stringify(res.results[0]));
            return res
          })
          .catch(err => {
            console.log(err);
            Vue.prototype.$message.error("网络连接错误" + "sytaskcorn");
          });
      },

