      },
      //获取详情信息
      getdatadetail(id, api) {
        return new Promise(resolve => {
          let allparams = {
            params: {
              id: id
            },
            api: api,
          }
          store
            .dispatch("getmsgData", allparams)
            .then(response => {
              // this.loading = false;
              let res = response.data;
              let row = null
              if (ifrrandnotnone(res.results)) {
                if (res.results.length == 1) {
                  row = res.results[0]
                } else if (res.results.length > 1) {
                  row = res.results
                  Vue.prototype.$message.warning("筛选出" + res.results.length + '条信息');
                } else {
                  Vue.prototype.$message.warning("该条信息可能已被删除，详情请联系管理员");
                  Vue.prototype.$eventBus.$emit("to404")
                }
              } else {
                if (res.length == 1) {
                  row = res[0]
                } else {
                  Vue.prototype.$message.warning("该条信息可能已被删除，详情请联系管理员");
                  Vue.prototype.$eventBus.$emit("to404")
                }
              }

              resolve(row)
            })
            .catch(err => {
              resolve(null)
              console.log(err);
              Vue.prototype.$message.error("网络连接错误,接口:" + api);
            });
        })
      },



