import Vue from 'vue';
/*
 *  定义公共js里，在入口文件main.js中import；
 *  给elementUI的dialog上加上 v-dialogDrag 指令就可以实现弹窗的全屏和拉伸了。
 */
// v-dialogDrag: 弹窗拖拽+水平方向伸缩
Vue.directive('dialogDrag', {
    bind(el, binding, vnode, oldVnode) {
        //弹框可拉伸最小宽高
        let minWidth = 400;
        let minHeight = 150;
        //初始非全屏
        let isFullScreen = false;
        //当前宽高
        let nowWidth = 0;
        let nowHight = 0;
        let nowTop = 0;
        let nowLeft = 0;
        //当前顶部高度
        let nowMarginTop = 0;
        //获取弹框头部（这部分可双击全屏）
        const dialogHeaderEl = el.querySelector('.el-dialog__header');
        //获取弹窗底部
        const dialogFooterEl = el.querySelector('.el-dialog__footer');
        // dialogFooterEl.style.bottom = '0';
        // dialogFooterEl.style.width = '100%';

        //弹窗最外层控制Zindex
        // const dragWrapper = el.querySelector('.el-dialog__wrapper');
        const dragWrapper = el;
        el.style.pointerEvents = 'none';
        //弹窗
        const dragDom = el.querySelector('.el-dialog');
        dragDom.style.display = 'flex';
        dragDom.style.flexDirection = 'column';
        dragDom.style.pointerEvents = 'auto';
        // dialogBodyEl.style.flex = '1';
        //给弹窗加上overflow auto；不然缩小时框内的标签可能超出dialog；
        // dragDom.style.overflow = "auto";
        //清除选择头部文字效果
        dialogHeaderEl.onselectstart = new Function("return false");
        //头部加上可拖动cursor
        dialogHeaderEl.style.cursor = 'move';

        // 获取原有属性 ie dom元素.currentStyle 火狐谷歌 window.getComputedStyle(dom元素, null);
        const sty = (function () {
            if (window.document.currentStyle) {
                return (dom, attr) => dom.currentStyle[attr];
            } else {
                return (dom, attr) => getComputedStyle(dom, false)[attr];
            }
        })();


        let moveDown = (e) => {
            // 鼠标按下，计算当前元素距离可视区的距离
            const disX = e.clientX - dialogHeaderEl.offsetLeft;
            const disY = e.clientY - dialogHeaderEl.offsetTop;

            const screenWidth = document.body.clientWidth; // body当前宽度

            const screenHeight = document.documentElement.clientHeight; // 可见区域高度(应为body高度，可某些环境下无法获取)
            const bodyHeight = document.body.clientHeight;

            const dragDomWidth = dragDom.offsetWidth; // 对话框宽度
            const dragDomheight = dragDom.offsetHeight; // 对话框高度
            const minDragDomLeft = dragDom.offsetLeft; //左边最边界

            const maxDragDomLeft = screenWidth - dragDom.offsetLeft - dragDomWidth;
            const minDragDomTop = dragDom.offsetTop;

            let maxDragDomTop = screenHeight - dragDom.offsetTop - dragDomheight;

            //判断当前页面的高度是否等于可见区域的高度
            if (screenHeight >= bodyHeight) {
                maxDragDomTop = screenHeight - dragDom.offsetTop - dragDomheight;
            }
            if (screenHeight < bodyHeight) {
                maxDragDomTop = bodyHeight - dragDom.offsetTop - dragDomheight;
            }
            // console.log(bodyHeight)
            // 获取到的值带px 正则匹配替换
            let styL = sty(dragDom, 'left');
            let styT = sty(dragDom, 'top');
            // 注意在ie中 第一次获取到的值为组件自带50% 移动之后赋值为px
            if (styL.includes('%')) {
                // eslint-disable-next-line no-useless-escape
                styL = +document.body.clientWidth * (+styL.replace(/\%/g, '') / 100);
                // eslint-disable-next-line no-useless-escape
                styT = +document.body.clientHeight * (+styT.replace(/\%/g, '') / 100);
            } else {
                styL = +styL.replace(/\px/g, '');
                styT = +styT.replace(/\px/g, '');
            }

            document.onmousemove = function (e) {
                // 通过事件委托，计算移动的距离
                let left = e.clientX - disX;
                let top = e.clientY - disY;

                // 边界处理
                if (-left > minDragDomLeft) {
                    left = -minDragDomLeft;
                } else if (left > maxDragDomLeft) {
                    left = maxDragDomLeft;
                }
                if (-top > minDragDomTop) {
                    top = -minDragDomTop;
                } else if (top > maxDragDomTop) {
                    top = maxDragDomTop;
                }

                // 移动当前元素
                dragDom.style.cssText += `;left:${left + styL}px;top:${top + styT}px;`;
            };

            document.onmouseup = function (e) {
                document.onmousemove = null;
                document.onmouseup = null;
            };


        }

        dialogHeaderEl.onmousedown = moveDown;



        //移动端手指点击事件
        dialogHeaderEl.addEventListener('touchstart', (e) => {
            // 鼠标按下，计算当前元素距离可视区的距离
            const disX = e.changedTouches[0].clientX - dialogHeaderEl.offsetLeft;
            const disY = e.changedTouches[0].clientY - dialogHeaderEl.offsetTop;

            // 获取到的值带px 正则匹配替换
            let styL = sty(dragDom, 'left');
            let styT = sty(dragDom, 'top');

            // 注意在ie中 第一次获取到的值为组件自带50% 移动之后赋值为px
            if (styL.includes('%')) {
                // eslint-disable-next-line no-useless-escape
                styL = +document.body.clientWidth * (+styL.replace(/\%/g, '') / 100);
                // eslint-disable-next-line no-useless-escape
                styT = +document.body.clientHeight * (+styT.replace(/\%/g, '') / 100);
            } else {
                styL = +styL.replace(/\px/g, '');
                styT = +styT.replace(/\px/g, '');
            }

            //移动端移动窗口事件
            dialogHeaderEl.addEventListener('touchmove', (e) => {

                // 通过事件委托，计算移动的距离
                let left = e.changedTouches[0].clientX - disX;
                let top = e.changedTouches[0].clientY - disY;

                // 移动当前元素
                dragDom.style.cssText += `;left:${left + styL}px;top:${top + styT}px;`;

            });

            // dialogHeaderEl.addEventListener('touchend', function () {
            //     dialogHeaderEl.addEventListener('touchmove',null);
            //     // document.addEventListener('touchend',null);
            // });


        });





        var dragDomList = document.getElementsByClassName('el-dialog__wrapper');
        var down_list_num = 0,
            up_list_num = 0;
        var down_after_add = false;

        dragDom.onmousedown = (e) => {
            down_list_num = 0;
            dragDomList.forEach(element => {
                if (element.style.display == "none") {
                    down_list_num += 1
                }
            });
            // console.log(down_list_num)
            down_after_add = false;
        };
        dragDom.onclick = (e) => {
            var dialogList = document.getElementsByClassName('el-dialog__wrapper')
            up_list_num = 0;
            dragDomList.forEach(element => {
                if (element.style.display == "none") {
                    up_list_num += 1
                }
            });
            // console.log(up_list_num)
            //判断点击当前元素之前和之后是否发生了新增弹窗，如果新增，代表是通过当前弹窗点击后新增的子弹窗，如果没有则是通过其他事件新增的弹窗
            if (down_list_num > up_list_num) {
                down_after_add = true
            }
            //得出当前最前面的图层
            var nums = []
            for (let index = 0; index < dialogList.length; index++) {
                const element = dialogList[index];
                nums.push(element.style.zIndex)

            }

            var num = Math.max(...nums);
            // 循环
            for (let i = 0; i < dialogList.length; i++) {

                if (num < dialogList[i].style.zIndex) {
                    num = dialogList[i].style.zIndex
                }
            }

            // 如果当前点击的小于最前的图层，那么就让点击图层成为最大值+1.

            var thisIndex = null
            if (dragWrapper) {
                thisIndex = parseInt(dragWrapper.style.zIndex)
                if (thisIndex < num && !down_after_add) {
                    //点击图层成为最大值 +1
                    dragWrapper.style.zIndex = parseInt(num) + 1;
                }
            }
            //  else {
            //     for (let i = 0; i < dialogList.length; i++) {
            //         if (dialogList[i].style.zIndex) {
            //             //将其他图层的减去总图层数
            //             dialogList[i].style.zIndex = parseInt(dialogList[i].style.zIndex) - dialogList.length;
            //         }
            //     }
            // }
        }


        //拉伸
        let resizeEl = document.createElement("div");
        dragDom.appendChild(resizeEl);
        //在弹窗右下角加上一个10-10px的控制块
        resizeEl.className = 'move-point';
        resizeEl.style.cursor = 'se-resize';
        resizeEl.style.position = 'absolute';
        resizeEl.style.textAlign = 'right';
        resizeEl.style.paddingRight = '2px';
        resizeEl.innerHTML =
            `<svg width="14px" height="14px" viewBox="0 0 14 14" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                <g id="界面展示" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                    <g id="BIM运维-首页-智慧东合大屏（房间列表）" transform="translate(-1145.000000, -874.000000)" fill="#8492A650">
                        <path d="M1159,886 L1159,888 L1157,888 L1157,886 L1159,886 Z M1155,886 L1155,888 L1153,888 L1153,886 L1155,886 Z M1151,886 L1151,888 L1149,888 L1149,886 L1151,886 Z M1147,886 L1147,888 L1145,888 L1145,886 L1147,886 Z M1159,882 L1159,884 L1157,884 L1157,882 L1159,882 Z M1155,882 L1155,884 L1153,884 L1153,882 L1155,882 Z M1151,882 L1151,884 L1149,884 L1149,882 L1151,882 Z M1159,878 L1159,880 L1157,880 L1157,878 L1159,878 Z M1155,878 L1155,880 L1153,880 L1153,878 L1155,878 Z M1159,874 L1159,876 L1157,876 L1157,874 L1159,874 Z" id="矩形-6"></path>
                    </g>
                </g>
            </svg>`
        resizeEl.style.height = '20px';
        resizeEl.style.width = '20px';
        resizeEl.style.right = '0px';
        resizeEl.style.bottom = '0px';

        //鼠标拉伸弹窗
        resizeEl.onmousedown = (e) => {

            const dragBody = dragDom.querySelector('.el-dialog__body');
            console.log(dragDom)
            //给弹窗加上overflow auto；不然缩小时框内的标签可能超出dialog；
            dragBody.style.overflow = "auto";
            dragBody.style.flex = '1';
            // 记录初始x位置
            const clientX = e.clientX;
            const clientY = e.clientY;
            // 鼠标按下，计算当前元素距离可视区的距离
            const disX = clientX - resizeEl.offsetLeft - 20;
            const disY = clientY - resizeEl.offsetTop - 20;

            document.onmousemove = function (e) {
                e.preventDefault(); // 移动时禁用默认事件
                // 通过事件委托，计算移动的距离 
                const x = e.clientX - disX; //这里如果dialog控制是居中的，所以水平拉伸效果是双倍
                const y = e.clientY - disY;
                //比较是否小于最小宽高
                dragDom.style.width = x > minWidth ? `${x}px` : minWidth + 'px';
                dragDom.style.height = y > minHeight ? `${y}px` : minHeight + 'px';
            };

            //拉伸结束
            document.onmouseup = function (e) {
                document.onmousemove = null;
                document.onmouseup = null;
                document.onmousedown = null;
                const screenWidth = document.body.clientWidth; // body当前宽度
                const screenHeight = document.documentElement.clientHeight; // 可见区域高度(应为body高度，可某些环境下无法获取)
                const boxWidth = dragDom.offsetWidth
                const boxHeight = dragBody.offsetHeight
                //判断拉伸小于全屏尺寸就标记为非全屏，否则标记为全屏
                if (boxWidth < screenWidth && boxHeight < screenHeight) {
                    isFullScreen = false;
                } else {
                    isFullScreen = true;
                }
            };
        }

        document.onmousemove = null;
        document.onmouseup = null;
        document.onmousedown = null;

        //双击头部效果
        dialogHeaderEl.ondblclick = (e) => {
            const screenWidth = document.body.clientWidth; // body当前宽度
            const screenHeight = document.documentElement.clientHeight; // 可见区域高度(应为body高度，可某些环境下无法获取)
            const dragBody = dragDom.querySelector('.el-dialog__body');
            // console.log(screenWidth)
            if (isFullScreen == false) {
                nowHight = dragDom.clientHeight;
                nowWidth = dragDom.clientWidth;
                nowTop = dragDom.style.top;
                nowLeft = dragDom.style.left;
                dragDom.style.left = 0;
                dragDom.style.top = 0;
                // dragDom.style.transform = "none";
                // dragBody.style.height = "798px";
                dragDom.style.height = screenHeight - 2 + 'px';
                dragDom.style.width = screenWidth - 2 + 'px';
                dragDom.style.marginTop = 0;
                console.log(dragDom.style.width)
                isFullScreen = true;
                // dialogHeaderEl.style.cursor = 'move';
                // dialogHeaderEl.onmousedown = null;
            } else {
                dragDom.style.left = nowLeft;
                // dragDom.style.transform = "translateX(-50%)";
                dragDom.style.height = nowHight + 'px';
                dragBody.style.height = "auto";
                dragDom.style.width = nowWidth + 'px';
                dragDom.style.top = nowTop;
                isFullScreen = false;
                // dialogHeaderEl.style.cursor = 'move';
                // dialogHeaderEl.onmousedown = moveDown;
            }
        }


    }
})









/**
 *  注意：如果是el-dialog弹出的表单el-form，最好不要把提交按钮当做el-form-item（elementUI文档是这么用的），放在el-dialog里。
 *  不然按钮会同el-dialog中标签一样被overflow；
 */