<template>
    <div class="dialog-box">
        <dy-dialog :appendToBody="false" class="dialog-tree dy-ui" maxHeight="600px" top="54px" right="10px"
            dialogHeadColor="rgba(0,0,0,0.9);" dialogBorder="0" dialogBgColor="rgba(0.2,0.2,0.2,0.7);" title="构件详情"
            @before-close='close' :visible.sync="show">
            <div class="dialog-body">
                <el-collapse v-model="activeNames">
                    <el-collapse-item class="Paramgroup" v-for="[key, value] in formed_ElData" :key='key' :title="key"
                        :name="key">
                        <div class="in-list" v-for="li in value" :key='li.index'>
                            <span class="list-title">{{ li.split(":")[0] }} </span>
                            <span class="list-text">{{ li.split(":")[1] }} </span>
                        </div>
                    </el-collapse-item>
                </el-collapse>
            </div>
            <div slot="footer" class="dialog-footer">
            </div>
        </dy-dialog>
    </div>
</template>



<script>
    import {
        mapState,
        mapGetters,
    } from "vuex";
    export default {
        data() {
            return {
                formed_ElData: null,
                activeNames: ['1',],
                show: false,
                ElData: null
            }
        },
        components: {},


        computed: {
            ...mapState({
                //dgp: state => state.dgp.dgp, //dgp对象
                //ElData: state => state.dgp.ElData, //dgp对象
            }),



        },
        watch: {
            ElData(n) {
                this.formed_ElData = new Map()
                if (!n)
                    return
                let groups = n.split(";")
                groups.forEach(group => {
                    let splitidx = group.indexOf(":")
                    this.formed_ElData.set(group.substring(0, splitidx), group.substring(splitidx + 1).split(","))
                });

            }
        },
        created() {

        },
        mounted() {

        },

        methods: {
            close() {
                //this.$store.dispatch('dgp/update_elementDialogShow', false)
                this.$parent.bimMenu.find(e => e.index === '7').isActive = false
            },

            Array2object(array) {
                var map = new Map()
                for (var i = 0; i < array.length; i += 2) {
                    map.set(array[i], array[i + 1])
                }
                return map
            }
        },

        beforeDestroy() { },
    }
</script>

<style scoped src="@/assets/three/custom2.css"></style>
<style scoped>
    .main-box {
        margin-top: 20px;
    }

    .el-collapse-item__content:nth-child(odd)> {
        border-right: 1px solid #475669;
        text-align: start;
        width: 50%;
        min-width: 100px;
        padding-left: 20px;
        display: flex;
        align-items: center;
        font-size: 12px;
        margin: 5px 0;
        white-space: pre-wrap;
        color: #C0C4CC;
    }

    .el-collapse-item__content:nth-child(even)> {
        width: 50%;
        padding: 0 20px;
        font-size: 12px;
        display: flex;
        align-items: center;
        margin: 5px 0;
        color: #fff;
    }

    .in-list {
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 20px;
        background: #47566930;
        border-bottom: 1px solid #475669;
    }

    .in-list:hover {
        background: #47566975;
    }

    .in-list span {
        font-size: 12px;
    }
</style>