<template>
    <div class="dialog-box">
        <dy-dialog :appendToBody="false" class="dialog-tree dy-ui" maxHeight="600px" dialogHeadColor="rgba(0,0,0,0.9);"
            dialogBorder="0" dialogBgColor="rgba(0.2,0.2,0.2,0.7);" title="图纸目录" @before-close='close'
            :visible.sync="show" top="10px" right="10px">
            <div class="dialog-body">
                <span>{{ subject }}</span>
                <el-tree show-checkbox class="filter-tree" node-key="id" :data="dwgtreeData" :props="defaultProps"
                    @node-click="handleNodeClick" :load="loadNode" lazy></el-tree>
            </div>
            <div slot="footer" class="dialog-footer">
            </div>
        </dy-dialog>
        <dy-dialog title="图纸详情" :visible.sync="dwgViewDialogShow" top="200px" right="10px">

        </dy-dialog>

        <dy-dialog :title="imgtitle" :visible.sync="imgDialogShow">
            <img :src="imgsrc" />
        </dy-dialog>
    </div>
</template>



<script>
    import {
        mapState
    } from "vuex";
    export default {
        data() {
            return {
                show: false,
                imgtitle: null,
                imgsrc: null,
                imgDialogShow: false,
                subject: null,
                dwgViewDialogShow: false, // 图纸详情页面
                dwgtreeData: null,
                defaultProps: {
                    children: 'children',
                    label: 'file_title'
                }

            }
        },
        components: {},

        computed: {



        },
        watch: {
            selected_id(n) {
                //获取关联图纸列表
                let allparams = {
                    params: {
                        elementId: n
                    },
                    api: 'bimViewer/getCategoryFold',
                }
                this.$store.dispatch('getmsgData', allparams).then(res => {
                    this.dwgtreeData = []
                    this.subject = res.data.res.subject
                    this.dwgtreeData = res.data.res.field
                })
            }
        },
        created() {

        },
        mounted() {

        },

        methods: {
            close() {
                this.dwgViewDialogShow = false
                this.show = false
            },

            handleNodeClick(data, node, obj) {
                console.log(data, node, obj)
                if (data.file_path.endsWith(".png") || data.file_path.endsWith(".jpg")) {
                    this.imgDialogShow = true
                    this.imgsrc = data.file_path
                    this.imgtitle = data.file_title
                }

            },

            loadNode(node, resolve) {
                if (node.level == 0) {
                    resolve(node.data)
                } else if (node.data.is_folder == 1) {
                    //树加载
                    let allparams = {
                        params: {
                            pid: node.data.id
                        },
                        api: 'fileMangement',
                    }
                    this.$store.dispatch('getmsgData', allparams).then(res => {
                        resolve(res.data)
                    })
                } else
                    resolve([])
            }

        },

        beforeDestroy() { },
    }
</script>

<style scoped src="@/assets/three/custom2.css"></style>
<style scoped>
    .main-box {
        margin-top: 20px;
    }
</style>