<template>
    <div class="three-engine dy-ui">
        <dy-loading1 v-if="loading" />
        <!-- 左上角树形结构 -->
        
        <bim-tree ref="bimtree" @treeClickSetTrans='treeClickSetTrans' />
        <!-- 左上角树形结构 -->
        <!-- 构件信息弹窗 -->
        <element-dialog ref="elementdialog" />
        <!-- 构件信息弹窗 -->
        <!-- 空间详情信息弹窗 -->
        <viewsheet-dialog />
        <!-- 空间详情信息弹窗 -->
        <rooms-dialog />
        <setting-dialog ref="settingdialog" />
        <!-- 设置界面弹窗 -->
        <info-dialog ref="infodialog" />
        <!-- 基本信息界面弹窗 -->
        <!-- <mod-dialog ref="moddialog"/> -->
        <!-- 修改材质界面弹窗 -->
        <div id="cesiumContainer" style="width:100%;height: 100vh;"></div>
        <div v-on:keyup.esc="console.log(1123)" class="dgp_container" :id='id'
            style="width:100%;position:absolute;top:0;background-color:transparent;"></div>
        <!-- 引擎底部BIM工具栏 -->
        <!-- <dy-menu-bim v-show="showTools" :bottom="model==='ops'?'10px':'10px'" @current-change="currentBimMenu"
            :menu="bimMenu"></dy-menu-bim> -->
        <!-- 引擎底部BIM工具栏 -->
        <dy-contextmenu-a v-show="showMenu" :eventInfo="eventInfo" :selected_id="selected_id" :menutree="menuTree" />
        <svg :id="`controltargetmark${id}`" style=" opacity: 0;" width="8px" height="8px" viewBox="0 0 8 8"
            version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
            <g id="模型详情" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd" opacity="0.699999988">
                <g id="模型详情-问题清单" transform="translate(-882.000000, -624.000000)" fill="#00988A" stroke="#FFFFFF">
                    <circle id="椭圆形" cx="886" cy="628" r="3.5"></circle>
                </g>
            </g>
        </svg>
   
    </div>
</template>

<script>
    import bimTree from "../tools/bim_tree" //目录树
    import elementDialog from "../dialog/element_dialog" //构件详情弹窗
    import viewsheetDialog from "../dialog/viewsheet_dialog" //构件详情弹窗
    import roomsDialog from "../dialog/rooms_dialog" //空间详情信息弹窗
    import settingDialog from "../dialog/setting_dialog"
    import infoDialog from "../dialog/info_dialog"
    import modDialog from "../dialog/mod_dialog" //修改材质弹窗
    import dwgtreeDialog from "../dialog/dwgtree_dialog" //图纸目录弹窗
    import Viewer3D, {
        MeasureMode
    } from '@/assets/three/Viewer3D'
    import {
        mapState
    } from "vuex";
    export default {
        viewer: null,
        data() {
            return {
                loading: true,
                loadDgp: false,
                dgp: null,
                fullscreen: false,
                selected_id: null,
                eventInfo: {}, //右击传递回调构件信息
                showMenu: false, //模型菜单控制
                elementDialogShow: false, // 构建信息弹窗显隐
                bimMenu: [{
                    index: '1',
                    helptext: '主视角',
                    img: require('@/assets/img/menu-bim/ic_home.svg'),
                    isActive: false,
                    noActive: true,
                },
                {
                    index: '3',
                    helptext: '测量',
                    img: require('@/assets/img/menu-bim/ic_straighten.svg'),
                    isActive: false,
                    children: [{
                        index: '3-1',
                        helptext: '距离',
                        img: require('@/assets/img/menu-bim/ic_distance.svg'),
                        isActive: false,
                    }, {
                        index: '3-2',
                        helptext: '面积',
                        img: require('@/assets/img/menu-bim/ic_area.svg'),
                        isActive: false,
                    }, {
                        index: '3-3',
                        helptext: '角度',
                        img: require('@/assets/img/menu-bim/ic_angle.svg'),
                        isActive: false,
                    }],
                }, {
                    index: '4',
                    helptext: '剖切',
                    img: require('@/assets/img/menu-bim/ic_flip.svg'),
                    isActive: false,
                    noActive: true,
                    children: [{
                        index: '4-2',
                        helptext: '轴向剖切',
                        img: require('@/assets/img/menu-bim/ic_trending_down.svg'),
                        isActive: false,
                    }, {
                        index: '4-3',
                        helptext: '剖切盒',
                        img: require('@/assets/img/menu-bim/ic_border_outer.svg'),
                        isActive: false,
                    }],
                }, {
                    index: '5',
                    helptext: '漫游模式',
                    img: require('@/assets/img/menu-bim/ic_directions_walk.svg'),
                    isActive: false,
                }, {
                    index: '7',
                    helptext: '构件详情',
                    img: require('@/assets/img/menu-bim/ic_assignment.svg'),
                    isActive: false,
                }, {
                    index: '8',
                    helptext: '设置',
                    img: require('@/assets/img/menu-bim/ic_settings.svg'),
                    isActive: false,
                }, {
                    index: '9',
                    helptext: '基本信息',
                    img: require('@/assets/img/menu-bim/ic_error.svg'),
                    isActive: false,
                }, {
                    index: '10',
                    helptext: '全屏',
                    img: require('@/assets/img/menu-bim/ic_fullscreen.svg'),
                    isActive: false,
                }
                ],
                menuTree: [
                    {
                        tag: "聚焦构件",
                        icon: require("@/assets/img/ic_gps_fixed.svg"),
                        fun: () => {
                            this.focusComponents()
                        }
                    },
                    {
                        tag: "构件状态",
                        icon: require("@/assets/img/ic_brightness_medium.svg"),
                        item: [
                            // {
                            //     tag: "修改材质",
                            //     fun: () => {
                            //         this.modmaterials()
                            //     }
                            // },
                            {
                                tag: "构件隐藏",
                                fun: () => {
                                    this.hiddenComponents()
                                }
                            },
                            {
                                tag: "构件闪烁",
                                fun: () => {
                                    this.set_comp_flash()
                                }
                            },
                            {
                                tag: "构件半透明",
                                fun: () => {
                                    this.translucentComponents()
                                }
                            },
                             {   tag: "构件冻结",
                                fun: () => {
                                    this.freezeComponents()
                                }
                            }, 
                            {
                                tag: "构件隔离（隐藏其他）",
                                fun: () => {
                                    this.isolationComponents()
                                }
                            },
                        ],
                        fun: () => {

                        }
                    }, {
                        tag: "构件信息",
                        icon: require("@/assets/img/ic_assignment.svg"),
                        fun: () => {
                            this.componentsDetails();
                        }
                    },
                    {
                        tag: "查找图纸",
                        icon: require("@/assets/img/ic_loop.svg"),
                        fun: () => {
                            this.searchDwg()
                        }
                    },
                    {
                        tag: "系统展示",
                        icon: require("@/assets/img/ic_system.svg"),
                        always_show: true,
                        fun: () => {
                            this.showSystem()
                        }
                    },
                    {
                        tag: "重置视图",
                        icon: require("@/assets/img/ic_show_all.svg"),
                        always_show: true,
                        fun: () => {
                            this.resetView()
                        }
                    },
                    {
                        tag: "取消选中",
                        icon: require("@/assets/img/ic_close.svg"),
                        fun: () => {
                            this.deselect()
                        }
                    }
                ],
            }
        },
        components: {
            bimTree,
            elementDialog,
            viewsheetDialog,
            roomsDialog,
            settingDialog,
            infoDialog,
            modDialog,
            dwgtreeDialog,
        },
        props: {
            id: {
                default: 'dgp_container',
            },
            //模型地址列表
            modelList: {
                default: null,
            },
            //应用模块
            model: {
                default: null,
            }
        },
        computed: {
            ...mapState({
                camerajson: state => state.dgp.camerajson, //原始视角
                showTools: state => state.dgp.showTools, //显隐工具栏
            }),
        },
        watch: {
            fullscreen(n) {
                let element = document.documentElement;
                if (!n) {
                    if (document.webkitCancelFullScreen) {
                        document.webkitCancelFullScreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                } else {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        // IE11
                        element.msRequestFullscreen();
                    }
                }
            },
            modelList(n) {
                if (n != null) {
                    this.init_dgp()
                    this.loadDgp = true
                }
            },
            selected_id:{
            handler:function(val){
                this.$refs.bimtree.defaultExpandedKeys=[val]
                this.$refs.bimtree.selectedRegion(val)
            },
            immediate:false,
            },
        },
        
        created() {

            //this.init()
        },
        mounted() {
            // 添加键盘Esc事件，注意此处要同时绑定到dgp的键盘监听
            this.init_dgp()
            // this.setDGP()
            this.$nextTick(() => {
                if (!this.loadDgp && this.modelList) {
                    this.init_dgp()
                }
            })
        },
        methods: {
            init_dgp() {
                this.loading = true
                const defaultSettings = {
                    unit: "file",
                    decimalPrecision: 99,
                    camera: {
                        near: 0.5,
                        far: 30000
                    },
                    mouse: {
                        sensitivity: 3
                    },
                    keyboard: {
                        sensitivity: 3
                    }
                };

                if (this.viewer) {
                    this.viewer.clearscene()
                }
                else {
                    let dom = document.getElementById(this.id)
                    const { clientHeight, clientWidth } = dom
                    this.viewer = new Viewer3D(this.id, clientWidth, window.innerHeight, defaultSettings); // full screen
                    dom.style.pointerEvents = "none";
                }

                // window.viewer = viewer
                const models = this.modelList ? this.modelList : [{
                    "name": "test",
                    "src": "H/东合地形图A2-2022.5.13.glb"
                }]

                this.viewer.loadModels(models).then(e => {
                    viewer.set_camera_json(JSON.stringify(this.camerajson))
                    this.$refs.bimtree.roomData = viewer.get_rooms()
                    this.$refs.bimtree.componentData = viewer.get_familyinstancetrees()
                    this.$refs.bimtree.worksetData = viewer.get_worksettrees()
                    this.$refs.infodialog.objInfo = viewer.get_ObjInfo()
                    this.$refs.bimtree.progressData = viewer.get_views()
                    console.log("模型加载完成！")
                    this.loading = false
                    if (this.model === 'ops') {
                        viewer.setbackground("#172534") //设置背景颜色
                    }

                })
                window.viewer = this.viewer
                console.log(this.viewer.get_camera_json())
                this.$store.dispatch('dgp/update_dgp', { id: this.id, dgp: this })


                //鼠标点击回调
                this.viewer.callMouseClick = event => {
                    if (event.button == 2) {
                        this.showMenu = true
                        this.eventInfo = event
                    } else {
                        this.showMenu = false
                    }
                }
                //选中构建回调
                this.viewer.callbackSelect = (id) => {
                    this.selected_id = id
                    //获取模型列表
                    if (id != null) {
                        let allparams = {
                            params: {
                                elementId: id
                            },
                            api: 'bimViewer/modelDetail',
                        }
                        this.$store.dispatch('getmsgData', allparams).then(res => {
                            this.$refs.elementdialog.ElData = res.data.count > 0 ? res.data.results[0].elementJson : null
                        })
                    }
                }
            },
            treeClickSetTrans(id) {
                console.log('dotreeClickSetTrans');
                this.viewer.clearSelection()
                this.viewer.set_comps_transparencyFix(id, 0.2,true,false,false)
            },
            currentBimMenu(item) {
                //点击了BIM菜单栏的第一个按钮
                if (item.index == '1') {
                    this.viewer.set_camera_json(JSON.stringify(this.camerajson))
                }

                //点击了BIM菜单栏的第二个按钮
                if (item.index == '2') {

                }
                //开启测量
                if (item.index == '3-1') {
                    if (item.isActive) {
                        console.log("开启了距离测量")
                        this.viewer.enableMeasure(MeasureMode.Distance);
                    } else {
                        console.log("关闭测量")
                        this.viewer.disableMeasure();
                    }
                }
                if (item.index == '3-2') {
                    if (item.isActive) {
                        console.log("开启了面积测量")
                        this.viewer.enableMeasure(MeasureMode.Area);
                    } else {
                        console.log("关闭测量")
                        this.viewer.disableMeasure();
                    }
                }
                if (item.index == '3-3') {
                    if (item.isActive) {
                        console.log("开启了面积测量")
                        this.viewer.enableMeasure(MeasureMode.Angle);
                    } else {
                        console.log("关闭测量")
                        this.viewer.disableMeasure();
                    }
                }

                //开启剖切
                if (item.index == '4-3') {
                    if (item.isActive) {
                        console.log("开启剖切盒")
                        this.viewer.enableSection();
                    } else {
                        console.log("关闭剖切")
                        this.viewer.disableSection();
                    }
                }
                if (item.index == '4-2') {
                    if (item.isActive) {
                        console.log("开启轴向剖切")
                        this.viewer.enableSection("PlaneSection");
                    } else {
                        console.log("关闭剖切")
                        this.viewer.disableSection();
                    }
                }

                if (item.index == '5') {
                    if (item.isActive) {
                        console.log("开启了漫游")
                        this.viewer.setflycontrol(true)
                    } else {
                        this.viewer.setflycontrol(false)
                        console.log("关闭了漫游")
                    }
                }
                //点击打开构件详情弹窗

                if (item.index == '7') {

                    this.$refs.elementdialog.show = item.isActive

                }
                // 点击打开设置界面
                if (item.index == '8') {
                    this.$refs.settingdialog.show = item.isActive
                }
                //点击信息功能键
                if (item.index == '9') {


                    this.$refs.infodialog.show = item.isActive

                }
                //点击全屏功能键
                if (item.index == '10') {
                    if (item.isActive) {
                        this.fullscreen = true
                    } else {
                        this.fullscreen = false
                    }
                }
                console.log(item)
            },

            focusComponents() {

                this.showMenu = false
                this.viewer.lookat_comp(this.selected_id)

            },
            hiddenComponents() {
                this.showMenu = false
                var id = this.selected_id
                this.viewer.clearSelection()
                this.viewer.resetflash()
                this.viewer.set_comps_transparencyFix([id], 0.2,false,false,false)
            },
            modmaterials() {
                this.showMenu = false
                this.$store.dispatch('dgp/update_modDialogShow', true)
            },
            translucentComponents() {
                this.showMenu = false
                var id = this.selected_id
                this.viewer.clearSelection()
                this.viewer.resetflash()
                this.viewer.set_comps_transparencyFix([id], 0.2,true,false,false)
            },
            freezeComponents() {
                this.showMenu = false
                var id = this.selected_id
                this.viewer.clearSelection()
                this.viewer.resetflash()
                // 如果存在 冻结，则存入
                this.viewer.lockList=[...new Set([...this.viewer.lockList,id])]
                this.viewer.set_comps_transparencyFix([id], 0.2, true,false,true)
            },
            isolationComponents() {
                this.showMenu = false
                var id = this.selected_id
                this.viewer.clearSelection()
                this.viewer.set_all_comps_visibility(false)
                this.viewer.set_comps_visiblity(id, true)
            },
            resetView() {
                this.showMenu = false
                this.viewer.clearSelection()
                // this.viewer.set_all_comps_visibility(true)
                console.log(this.$refs.bimtree.$refs.componentTree);
                this.viewer.lockList = []
                this.viewer.reset_AllCompTransparentfix()
            },
            deselect() {
                this.showMenu = false
                this.viewer.clearSelection()
            },
            searchDwg() {
                this.showMenu = false
                this.$store.dispatch('dgp/update_dwgtreeDialogShow', true)
            },
            componentsDetails() {
                this.showMenu = false
                this.$refs.elementdialog.show = true

            },
            set_all_comps_visibility(show) {
                this.viewer.set_all_comps_visibility(show)
            },

            set_camera_json(json) {
                this.viewer.set_camera_json(json)
            },

            get_camera_json(json) {
                return this.viewer.get_camera_json(json)
            },
            lookat_comp(id) {
                this.viewer.lookat_comp(id)
            },
            lookat_room(id) {
                this.viewer.lookat_room(id)
            },
            insert_2d_Mark(ids, dom, id) {
                this.viewer.insert_2d_Mark(ids, dom, id)
            },
            show_Css2dMark(ids, show) {
                this.viewer.show_Css2dMark(ids, show)
            },
            get_rooms(src) {
                return this.viewer.get_rooms(src)
            },
            set_comps_visiblity(id, show) {
                this.viewer.set_comps_visiblity(id, show)
            },
            setShowByID(id) {
                this.viewer.setShowByID(id)
            },
            set_comps_transparency(id, v, lock, color, top) {
                this.viewer.set_comps_transparency(id, v, lock, color, top)
            },
            setHiddenByID(id, v, lock, color, top) {
                this.viewer.setHiddenByID(id, v, lock, color, top)
            },
            reset_CompTransparent(ids) {
                this.viewer.reset_CompTransparentfix(ids)
            },
            setAmlight(i) {
                this.viewer.setAmlight(i)
            },
            setDirlight(i) {
                this.viewer.setDirlight(i)
            },
            setHemlight(i) {
                this.viewer.setHemlight(i)
            },
            setPotlight(i) {
                this.viewer.setPotlight(i)
            },
            setCamlight(i) {
                this.viewer.setCamlight(i)
            },
            gotoViewbyName(s) {
                this.viewer.gotoViewbyName(s)
            },
            setToOrthographicCamera(e) {
                this.viewer.setToOrthographicCamera(e);
            },
            setTranspMode(e) {
                this.viewer.setTranspMode(e)
            },
            setWireframeMode(e) {
                this.viewer.setWireframeMode(e)
            },
            setbackground(e) {
                this.viewer.setbackground(e)
            },

            set_selected_mat(s, r, m) {
                this.viewer.set_selected_mat(s, r, m)
            },
            setRenderReduce(on) {
                this.viewer.setRenderReduce(on)
            },

            setkeyEvent(on) {
                this.viewer.setkeyEvent(on)
            },//键盘事件是否触发， 传true时触发，false取消触发

            resize(w, h) {
                this.viewer.resize(w, h)
            },
            //是否开启窗口变形监听
            setResizeListener(on) {
                this.viewer.setResizeListener(on)
            },
            set_comp_flash(){
                this.showMenu = false
                var id = this.selected_id
                this.viewer.clearSelection()
                this.viewer.set_comp_flash([id])
            },

            set_keeprender() { },
            addOutline() { },
            setAggrParam() { },
            set_comps_selected() { },
            reset_clips() { },
            remove_edge() { },
            showbyJson() { }
        },
        beforeDestroy() {
            this.viewer.beforeDestroy();
            this.viewer = undefined;
        }

    }
</script>
<style scoped src="@/assets/three/custom2.css"></style>
<style scoped>
    /* @import '@/assets/three/custom.css'; */
    .controltargetanimate {
        animation: hidetip 0.3s;
    }


    /* .dgp_container {
        background: linear-gradient(180deg, #4788d2 0%, #f8f9f9 100%);
    } */

    .three-engine {
        position: relative;
    }
    /deep/ .cesium-viewer-bottom {
        display:none;
    }

    @keyframes hidetip {
        from {
            opacity: 1;
        }

        to {
            opacity: 0;
        }
    }

    @-webkit-keyframes hidetip {
        from {
            opacity: 1;
        }

        to {
            opacity: 0;
        }
    }
</style>