<!-- 当前视图为业务层 业务相关的请在当前视图下创建 -->
<template>
    <div class="home" style="position: relative;">
        <!-- <viewer-header :title="title" /> -->
        <!-- dgp三维引擎 -->
        <header-default :isIoc='true' />
        <dgp custom_style="width:100%;position:absolute" :modelList="modelList" :model="model" id="123" />
        <side-bar-main />
        <!-- dgp三维引擎 -->
    </div>
    </template>
    
    <script>
    import dgp from "@/components/dgp/dgp_engine" //模型组件
   // import viewerHeader from "@/components/viewerHeader/viewerHeader"; // 预览视图头部
    import headerDefault from "@/components/opsViewer/header/header_default" //头部组件
    import sideBarMain from "@/components/opsViewer/sidebar/side_bar_main" //侧边栏
    export default {
        name: 'home',
        data() {
            return {
                //模型路径
                modelList: null,
                title: null,
            }
        },
        props: {
            //模型地址列表
            model: {
                default: null,
            },
        },
        watch: {
            modelList(n) {
                this.title = n[0].name
            }
        },
        computed: {
    
        },
        components: {
            dgp,
            headerDefault,
            sideBarMain,
        },
    
        created() {
    
        },
        mounted() {
            // this.getModelPath()
        },
        methods: {
            //通过获取路由模型ID请求模型详情中的地址，传入dgp引擎打开模型
            getModelPath() {
                let id = this.$route.query.id
                let name = this.$route.query.name
                let path = this.$route.query.path
                if (id) {
                    this.$store.dispatch('getmsgData', {
                        api: 'bimViewer/modelList',
                        params: {
                            id,
                        }
                    }).then(res => {
                        let apiData = res.data.results[0] //模型列表
                        let modelPath = res.data.results[0].filePath.parsePath
                        let modelName = res.data.results[0].fileName
                        let modelList = [{
                            name: modelName,
                            src: modelPath
                        }]
                        this.modelList = modelList
                    })
                }
                if (path) {
                    let modelList = [{
                        name: name,
                        src: path
                    }]
                    this.modelList = modelList
                }
            },
    
        }
    }
    </script>
    
    <style scoped>
    .home {
        height: 100vh;
        overflow: hidden;
    }
    
    </style>
    