<template>
    <div class="dialog-box">
        <dy-dialog :appendToBody="false" class="dialog-tree dy-ui" top="10px" right="10px"
            dialogHeadColor="rgba(0,0,0,0.9);" dialogBorder="0" dialogBgColor="rgba(0.2,0.2,0.2,0.7);" :title='svgtitle'
            @before-close='close' :visible.sync="viewsheetDialogShow">
            <div class="dialog-body">
                <inline-svg :src=svgsrc width=600 height=600 aria-label="My image" @loaded="svgLoaded($event)"
                    ref="viewsheetsvg"></inline-svg>
            </div>
            <div slot="footer" class="dialog-footer">
            </div>
        </dy-dialog>
    </div>
</template>

<script>
    import InlineSvg from 'vue-inline-svg';
    import {
        mapState
    } from "vuex";
    const targetsize = 600
    export default {
        data() {
            return {
                viewsheetscale: 1,
            }
        },
        components: {
            InlineSvg,
        },

        computed: {
            svgsrc() {
                return `H/viewsheets/${this.Svg_Src}.svg`
            },

            svgtitle() {
                return `图纸:  ${this.Svg_Src}`
            },

            ...mapState({
                dgp: state => state.dgp.dgp, //dgp对象
                Svg_Src: state => state.dgp.SvgSrc, //图纸
            }),

            //图纸弹窗显示
            viewsheetDialogShow: {
                get() {
                    return this.$store.state.dgp.viewsheetDialogShow
                },
                set() {

                }
            },
        },
        watch: {

        },
        created() {

        },
        mounted() {

        },

        methods: {
            close() {
                this.$store.dispatch('dgp/update_viewsheetDialogShow', false)
            },
            svgLoaded(svg) {
                svg.addEventListener("mousewheel", (e) => {
                    this.viewsheetscale *= Math.pow(2, -e.deltaY / 1500)
                    var trans = targetsize * (this.viewsheetscale - 1) / 2
                    svg.setAttribute("transform", `scale(${this.viewsheetscale},${this.viewsheetscale}),translate(${trans},${trans})`);
                })
                svg.addEventListener("click", (e) => {
                    //console.log(e.target.getAttribute("class"))
                    if (e.target.getAttribute("class") == "svg-roomTag")
                        this.dgp.set_comps_visiblity(e.target.id, true)
                    this.dgp.lookat_comp(e.target.id)
                })
            }
        },

        beforeDestroy() { },
    }
</script>
<style scoped src="@/assets/three/custom2.css"></style>
<style scoped>
    .main-box {
        margin-top: 20px;
    }
</style>