<template>
<dy-sidebar-group space="10px" borderRadius="2px" borderWidth="0" backgroundColor="rgba(5, 13, 24, 0.75)" :heightType="5">
    <dy-sidebar-tittle3 titleMargin="5px" :title_CN="'楼栋设备运行状态'" :show_EN="false" />
    <div class="weather_around" ref="weather_around">
        <div class="around">
            <div class="total-num">
                <div><span class="tag-point" style="background:#00B0A1;"></span>
                    <p>运行：<span>{{totalRunning}}</span></p>
                </div>
                <div><span class="tag-point" style="background:#A2BBBC;"></span>
                    <p>关闭：<span>{{totalShutdown}}</span></p>
                </div>
                <div><span class="tag-point" style="background:#FF4949;"></span>
                    <p>故障：<span>{{totalFailure}}</span></p>
                </div>
            </div>

            <div class="drainage">

                <div v-for="(item,index) in runningData" :key="index" class="drainage_item">
                    <!-- SVG装饰角 -->
                    <svg v-for="(item,index) in tagStyle" :class="`tag ${item}`" width="5px" height="5px" viewBox="0 0 5 5" version="1.1">
                        <g transform="translate(-76.000000, -85.000000)">
                            <g transform="translate(76.000000, 85.000000)" fill="#00B0A1">
                                <path d="M5,2.22044605e-16 L5,1 L1,0.999 L1,5 L0,5 L0,0 L5,2.22044605e-16 Z"></path>
                            </g>
                        </g>
                    </svg>
                    <!-- SVG装饰角 -->

                    <!-- 卡片内容 -->
                    <div class="card-box">
                        <img :src="item.img">
                        <div class="drainage_text">
                            <p>{{item.equipmentName}}</p>
                            <div style="color:#A2BBBC;"><span style="color:#00B0A1;"> {{item.running}} </span> /
                                <span style="color:#A2BBBC;">{{item.shutdown}}</span> / <span style="color:#FF4949;">{{item.failure}}</span>
                            </div>
                        </div>
                    </div>
                    <!-- 卡片内容 -->
                </div>

            </div>

        </div>
    </div>
</dy-sidebar-group>
</template>

<script>
export default {
    name: "side_bar_electricity",
    data() {
        return {
            //box高度
            boxheight: 50,
            //装饰角样式
            tagStyle: ["left-top", "right-top", "right-bottom", "left-bottom"],
            opsHomeDeviceStatus: {
                waterSupply: {
                    failure: 0,
                    running: 2,
                    shutdown: 0,
                },
                pvInv: {
                    failure: 0,
                    running: 4,
                    shutdown: 0,
                },
                elevator: {
                    failure: 0,
                    running: 12,
                    shutdown: 0,
                },
                ac: {
                    failure: 1,
                    running: 123,
                    shutdown: 708,
                },
                freshAir: {
                    failure: 0,
                    running: 0,
                    shutdown: 58,
                },
                light: {
                    failure: 0,
                    running: 105,
                    shutdown: 127,
                },
                env: {
                    failure: 0,
                    running: 187,
                    shutdown: 0,
                },
                cam: {
                    failure: 0,
                    running: 157,
                    shutdown: 0,
                },
            },

        }
    },
    mounted() {

    },
    computed: {
        //设备运行状态值
        runningData() {

            return [{
                    img: require("@/components/opsViewer/img/opsHomeEnv/drainage_new.svg"), //设备图标
                    equipmentName: "给排水设备", //设备名称
                    failure: this.opsHomeDeviceStatus.waterSupply.failure,
                    running: this.opsHomeDeviceStatus.waterSupply.running,
                    shutdown: this.opsHomeDeviceStatus.waterSupply.shutdown,
                },

                {
                    img: require("@/components/opsViewer/img/opsHomeEnv/fire_equip.svg"), //设备图标
                    equipmentName: "光伏发电", //设备名称
                    failure: this.opsHomeDeviceStatus.pvInv.failure,
                    running: this.opsHomeDeviceStatus.pvInv.running,
                    shutdown: this.opsHomeDeviceStatus.pvInv.shutdown,
                },
                {
                    img: require("@/components/opsViewer/img/opsHomeEnv/elevator_equip.svg"), //设备图标
                    equipmentName: "电梯", //设备名称
                    failure: this.opsHomeDeviceStatus.elevator.failure,
                    running: this.opsHomeDeviceStatus.elevator.running,
                    shutdown: this.opsHomeDeviceStatus.elevator.shutdown,
                },
                {
                    img: require("@/components/opsViewer/img/opsHomeEnv/power_equip.svg"), //设备图标
                    equipmentName: "暖通", //设备名称
                    failure: this.opsHomeDeviceStatus.ac.failure,
                    running: this.opsHomeDeviceStatus.ac.running,
                    shutdown: this.opsHomeDeviceStatus.ac.shutdown,
                },
                {
                    img: require("@/components/opsViewer/img/opsHomeEnv/exhaust_equip.svg"), //设备图标
                    equipmentName: "新风换气机", //设备名称
                    failure: this.opsHomeDeviceStatus.freshAir.failure,
                    running: this.opsHomeDeviceStatus.freshAir.running,
                    shutdown: this.opsHomeDeviceStatus.freshAir.shutdown,
                },
                {
                    img: require("@/components/opsViewer/img/opsHomeEnv/lighting_equip.svg"), //设备图标
                    equipmentName: "智能照明", //设备名称
                    failure: this.opsHomeDeviceStatus.light.failure,
                    running: this.opsHomeDeviceStatus.light.running,
                    shutdown: this.opsHomeDeviceStatus.light.shutdown,
                },
                {
                    img: require("@/components/opsViewer/img/opsHomeEnv/photovoltaic_equip.svg"), //设备图标
                    equipmentName: "环境感知", //设备名称
                    failure: this.opsHomeDeviceStatus.env.failure,
                    running: this.opsHomeDeviceStatus.env.running,
                    shutdown: this.opsHomeDeviceStatus.env.shutdown,
                },
                {
                    img: require("@/components/opsViewer/img/opsHomeEnv/photovoltaic_equip.svg"), //设备图标
                    equipmentName: "安防视频", //设备名称
                    failure: this.opsHomeDeviceStatus.cam.failure,
                    running: this.opsHomeDeviceStatus.cam.running,
                    shutdown: this.opsHomeDeviceStatus.cam.shutdown,
                },
            ]
        },

        totalRunning() {
            return this.opsHomeDeviceStatus.waterSupply.running + this.opsHomeDeviceStatus.pvInv.running + this.opsHomeDeviceStatus.elevator.running + this.opsHomeDeviceStatus.ac.running + this.opsHomeDeviceStatus.freshAir.running + this.opsHomeDeviceStatus.light.running + this.opsHomeDeviceStatus.env.running + this.opsHomeDeviceStatus.cam.running
        },
        totalShutdown() {
            return this.opsHomeDeviceStatus.waterSupply.shutdown + this.opsHomeDeviceStatus.pvInv.shutdown + this.opsHomeDeviceStatus.elevator.shutdown + this.opsHomeDeviceStatus.ac.shutdown + this.opsHomeDeviceStatus.freshAir.shutdown + this.opsHomeDeviceStatus.light.shutdown + this.opsHomeDeviceStatus.env.shutdown + this.opsHomeDeviceStatus.cam.shutdown
        },
        totalFailure() {
            return this.opsHomeDeviceStatus.waterSupply.failure + this.opsHomeDeviceStatus.pvInv.failure + this.opsHomeDeviceStatus.elevator.failure + this.opsHomeDeviceStatus.ac.failure + this.opsHomeDeviceStatus.freshAir.failure + this.opsHomeDeviceStatus.light.failure + this.opsHomeDeviceStatus.env.failure + this.opsHomeDeviceStatus.cam.failure
        },

    },
    watch: {

    },
    methods: {

        // //ops-首页-楼栋设备运行状态
        // getHomeDeviceStatus() {
        //     this.$store.dispatch("getOpsHomeDeviceStatus").then(res => {
        //         this.$store.commit('update_opsHomeDeviceStatus', res.data.res)
        //     }).catch(err => {
        //         this.$message.error('网络错误！')
        //     })
        // }
    },
    beforeDestroy() {

    }
}
</script>

<style scoped>
.weather_around {
    display: flex;
    overflow-y: hidden;
    flex: 1;
}

.total-num {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 300px;
    height: 30px;
    margin-left: 10px;
    margin-bottom: 5px;
    background: #99A9BF20;
}

.total-num div {
    color: #99A9BF;
    font-size: 13px;
    display: flex;
    align-items: center;
}

.tag-point {
    display: inline-block;
    width: 5px;
    height: 5px;
    background: #FFF;
    margin-right: 10px;
}

.around {
    width: 320px;
    display: flex;
    flex-direction: column;

}

.drainage {
    flex: 1;
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    flex-wrap: wrap;
    width: 300px;
    /* height: 100%; */
    margin-left: 10px;
    margin-bottom: 5px;
}

.drainage_item {
    width: 147px;
    /* height: 50px; */
    margin-right: 5px;
    margin-bottom: 5px;
    background: #99A9BF20;
    display: flex;
    align-items: center;
    justify-content: start;
    position: relative;
}

.tag {
    position: absolute;
}

.left-top {
    top: 0;
}

.right-top {
    top: 0;
    right: 0;
    transform: rotateY(180deg);
}

.right-bottom {
    bottom: 0;
    right: 0;
    transform: rotateY(180deg) rotateX(180deg);
}

.left-bottom {
    bottom: 0;
    left: 0;
    transform: rotateX(180deg);
}

.drainage>div:nth-child(even) {
    margin-right: 0;
}

.card-box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
}

.card-box img {
    width: 32px;
    height: 32px;
    margin-left: 10px;
    margin-right: 10px;
}

.drainage_text {
    /* height: 100%; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* padding: 10px 0; */
    /* align-items: center; */
}

.drainage_text p {
    font-size: 12px;
    color: #99A9BF;
    /* margin: 5px 0; */
}

.drainage_text div {
    font-size: 14px;
    /* margin-bottom: 5px; */
}
</style>
