<template>
  <div class="bim-tree dy-ui">
    <div v-if="btnShow" class="tree-btn" @click="btnShow = false">
      <img src="@/assets/img/tree-bim/ic_list.svg" alt="" title="目录树" />
    </div>
    <dy-dialog :appendToBody="false" class="dialog-tree dy-ui" maxHeight="600px" top="10px" left="10px"
      dialogHeadColor="rgba(0,0,0,0.9);" dialogBorder="0" dialogBgColor="rgba(0,0,0,0.75);" title="目录树"
      @before-close="close" :visible.sync="dialogShow">
      <div class="dialog-body">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="构件" name="component">
            <el-input
              size="mini"
              placeholder="输入构件关键字进行过滤"
              v-model="filterComponent"
            ></el-input>
            <el-tree
              show-checkbox
              class="filter-tree"
              node-key="id"
              :highlight-current="true"
              :data="componentData"
              :props="componentProps"
              :filter-node-method="filterComponentFun"
              ref="componentTree"
              :accordion="true"
              :default-expanded-keys="defaultExpandedKeys"
              @check-change="handlecheckchange"
              @check="handleComponentCheck"
              @node-click="handlecompNodeClick"
            >
              <span class="custom-tree-node" slot-scope="{ node, data }">
                <span>
                  <i class="el-icon-view" :class="{ active: !data.isViewIconShow }"
                    @click.stop="() => changeNodeStatus(node, data,'componentTree')"></i>
                </span>
                <span>{{ node.label }}</span>
              </span></el-tree>
          </el-tab-pane>
          <el-tab-pane label="工作集" name="major">
            <el-input
              size="mini"
              placeholder="输入专业关键字进行过滤"
              v-model="filterMajor"
            ></el-input>
            <el-tree
              show-checkbox
              class="filter-tree"
              node-key="id"
              :highlight-current="true"
              :accordion="true"
              :data="worksetData"
              :props="majorProps"
              :filter-node-method="filterComponentFun"
              :default-expanded-keys="defaultExpandedKeys"
              @node-click="handlecompNodeClick"
              ref="majorTree"
              @check="handleComponentCheck"
              @check-change="handlecheckchange"
            >
            <span class="custom-tree-node" slot-scope="{ node, data }">
                <span>
                  <i
                    class="el-icon-view"
                    :class="{ active: !data.isViewIconShow }"
                    @click.stop="() => changeNodeStatus(node, data,'majorTree')"
                  ></i>
                </span>
                <span>{{ node.label }}</span>
              </span>
            </el-tree>
          </el-tab-pane>
          <el-tab-pane label="空间" name="space"
            >空间
            <el-tree
              show-checkbox
              class="filter-tree"
              node-key="id"
              :default-checked-keys="[1]"
              :default-expanded-keys="[1]"
              :data="roomData"
              :props="majorProps"
              :filter-node-method="filterMajorFun"
              @node-click="handleRoomNodeClick"
              @check="handleRoomCheck"
            >
            </el-tree>
          </el-tab-pane>
           <el-tab-pane label="施工分类" name="progress">施工分类
                    <el-tree  show-checkbox class="filter-tree" node-key="id" :default-checked-keys="[1]" :default-expanded-keys="[1]" :data="progressData" :props="majorProps" :filter-node-method="filterMajorFun"  @node-click="handlecompNodeClick"></el-tree>
                </el-tab-pane> 
        </el-tabs>
      </div>
      <div slot="footer" class="dialog-footer"></div>
    </dy-dialog>
  </div>
</template>

<script>
  import { mapState } from "vuex";
  //import lodash from "lodash";
  export default {
    data() {
      return {
        btnShow: true, //显示目录树按钮
        dialogShow: false, //是否显示目录树弹窗
        activeName: "component", //目录树默认tab栏
        filterComponent: "", //构件树筛选,
        filterMajor: "", //专业树筛选,
        //构件树值
        componentData: null,
        //工作集树值
        worksetData: null,
        //房间树
        roomData: null,
        //施工分类树
        progressData:null,
        activestep: 0,
        steps: [],
        checkedids: [],
        checkedcomponents: [],
        checkedrooms: [],
        // viewsheetData: [],

        //构件排序规则
        componentProps: {
          children: "children",
          label: "label",
        },
        //专业排序规则
        majorProps: {
          children: "children",
          label: "label",
        },
        //默认展开的节点
        defaultExpandedKeys: [],
      };
    },
    components: {},

    computed: {
      ...mapState({
        dgp: (state) => state.dgp.dgp, //dgp对象
      }),
    },

    watch: {
      btnShow(n) {
        if (n) {
          this.dialogShow = false;
        } else {
          this.dialogShow = true;
        }
      },
      //更新构件筛选值后执行
      filterComponent(val) {
        this.$refs.componentTree.filter(val);
      },
      //更新专业筛选值后执行
      filterMajor(val) {
        this.$refs.majorTree.filter(val);
      },
      componentData(val) {
        this.steps = val[0].children;
        console.log(val[0].children);
      },
    },
    created() { },
    mounted() {
      this.$refs.componentTree.setCheckedKeys(["root"]);
      this.$refs.majorTree.setCheckedKeys(["root"]);
    },

    methods: {
      traverse(data) {
        data.children.forEach((element) => {
          if (element.children.length > 0) {
            this.traverse(element);
          } else {
            this.checkedids.push(element.id);
          }
        });
      },
      nextstep() {
        if (this.activestep++ > this.componentData[0].children.length - 1) {
          this.activestep = 0;
          this.$parent.set_all_comps_visibility(false);
        } else {
          this.checkedids = [];
          this.traverse(this.componentData[0].children[this.activestep - 1]);
          this.$parent.set_comps_visiblity(this.checkedids, true);
        }
      },
      close() {
        this.btnShow = true;
      },
      handleClick(tab, event) {
        console.log(tab, event);
        //if (tab.name == "progress") this.$parent.set_all_comps_visibility(false);
      },
      searchNode(data, id, replaceData) {
        let type = Object.prototype.toString
          .call(data)
          .split(" ")[1]
          .slice(0, -1);
        if (type === "Object") {
          if (data.id === id) {
            data = replaceData;
            return;
          }
          if (Array.isArray(data.children)) {
            data.children.forEach((item) => {
              this.searchNode(item, id, replaceData);
            });
          }
        }
      },
      changeDataStatus(data, status, selectId) {
        let type = Object.prototype.toString
          .call(data)
          .split(" ")[1]
          .slice(0, -1);
        data.label = new String(data.label)//触发刷新渲染
        if (type === "Object") {
          selectId.push(data.id);
          data.isViewIconShow = status;
          if (data.children && data.children.length) {
            data.children.forEach((item) => {
              this.changeDataStatus(item, status, selectId);
            });
          }}},
    // 使该节点以下的数据透明
    changeNodeStatus(node, data,refName) {
      let status = !data.isViewIconShow;
      let selectkeys= this.$refs[refName].getCheckedKeys()
      let selectId = [];
      this.changeDataStatus(data, status, selectId);
      this.searchNode(this.componentData[0], data.id, data);
      if(status){
        this.$parent.viewer.opacitylist=[...new Set([...this.$parent.viewer.opacitylist,...selectId])]
      }else{
        this.$parent.viewer.opacitylist = this.$parent.viewer.opacitylist.filter(item=>{
          return !selectId.includes(item)
        })
      }
      selectId=selectId.filter(item=>{
        return selectkeys.includes(item)
      })
      status
        ? this.treeClickSetTrans(selectId)
        : this.$parent.reset_CompTransparent(selectId);
    },
    searchNode(data, id, replaceData) {
      let type = Object.prototype.toString
        .call(data)
        .split(" ")[1]
        .slice(0, -1);
      if (type === "Object") {
        if (data.id === id) {
          data = replaceData;
          return;
        }
      }},
      //tree点击使 对应的节点透明
      treeClickSetTrans(selectId) {
        this.$emit("treeClickSetTrans", selectId);
      },
      setDefaultExpandedKeys() {
        // 获取树形组件实例
        let treeCompInstance = this.$refs["componentTree"];
        let allNodes = treeCompInstance.store._getAllNodes();
        let defaultExpandedNodes = [];
        allNodes.forEach((node) => {
          node.expanded && defaultExpandedNodes.push(node.data.id);
        });
        this.defaultExpandedKeys = defaultExpandedNodes;
      },
      //构件筛选函数
      filterComponentFun(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      //专业筛选函数
      filterMajorFun(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      //房间树点击
      handleRoomNodeClick(data, node, obj) {
        if (node.isLeaf) {
          this.$parent.set_comps_visiblity(data.id, true);
          this.$parent.lookat_room(data.id);
        }
    },
    changeDataStatus(data, status, selectId) {
      let type = Object.prototype.toString
        .call(data)
        .split(" ")[1]
        .slice(0, -1);
      data.label = new String(data.label); //触发刷新渲染
      if (type === "Object") {
        selectId.push(data.id);
        data.isViewIconShow = status;
        if (data.children && data.children.length) {
          data.children.forEach((item) => {
            this.changeDataStatus(item, status, selectId);
          });
        }
      }
    },
    //构件筛选函数
    filterComponentFun(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    //专业筛选函数
    filterMajorFun(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    //房间树点击
    handleRoomNodeClick(data, node, obj) {
      if (node.isLeaf) {
        this.$parent.set_comps_visiblity(data.id, true);
        this.$parent.lookat_room(data.id);
      }
    },
    //构件树点击定位
    handlecompNodeClick(data, node, obj) {
      if (node.isLeaf) {
        this.$parent.set_comps_visiblity(data.id, true);
        this.$parent.lookat_comp(data.id);
      }
    },
    handleComponentCheck(data, tree) {
      console.log('treechange',data,tree);
      let list = []
      this.getcheckchangeID(data,list)
      this.$parent.setHiddenByID(list);
      list=list.filter(item=>{
    return tree.checkedKeys.includes(item)
})
      
      this.$parent.setShowByID(list);
    },
    handlecheckchange(data, is, isc) {
      
    },
    getcheckchangeID(data, list) {
      let type = Object.prototype.toString
        .call(data)
        .split(" ")[1]
        .slice(0, -1);
      data.label = new String(data.label); //触发刷新渲染
      if (type === "Object") {
        list.push(data.id);
        if (data.children && data.children.length) {
          data.children.forEach((item) => {
            this.getcheckchangeID(item, list);
          });
        }
      }
    },
    handleRoomCheck(data, tree) {
      this.$parent.reset_CompTransparent(this.checkedrooms);
      this.$parent.set_comps_visiblity(this.checkedrooms, false);
      this.$parent.set_comps_visiblity(tree.checkedKeys, true);
      this.$parent.set_comps_transparency(
        tree.checkedKeys,
        0.3,
        false,
        0x00ffff,
        true
      );
      this.checkedrooms = tree.checkedKeys;
    },
    handleViewNodeClick(data, node, obj) {},

      getLeafids(data, ids) {
        if (data.children.length == 0) {
          ids.push(data.id);
        } else
          for (var i = 0; i < data.children.length; i++) {
            this.getLeafids(data.children[i], ids);
          }
      },
    //滚动到选中定位的位置
    selectedRegion(name) {
      let activeTree = this.activeName+'Tree'
      if(!this.$refs[activeTree])
        return
      if (this.oldId) {
        let data = this.$refs[activeTree].getNode(this.oldId);
        data.parent.expanded = false;
        data.parent.parent.expanded = false;
        data.parent.parent.parent.expanded = false;
      }

      this.oldId = name;
      this.$refs[activeTree].setCurrentKey(name);
    },
  },
  beforeDestroy() {},
};
</script>
<style scoped src="@/assets/three/custom2.css"></style>
<style scoped>
  /* @import '@/assets/three/custom2.css'; */

.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: left;
  font-size: 14px;
  padding-right: 8px;
}
/deep/ .el-tree-node__content:hover {
  background: rgba(255, 255, 255, 0.05);
}

/deep/.el-tree--highlight-current
  .el-tree-node.is-current
  > .el-tree-node__content {
  background-color: rgba(35, 220, 205, 0.78);
}

/deep/.el-tree {
  background: #10498f;

  color: #ffffff;
}

/deep/ .el-tree-node:focus > .el-tree-node__content {
  background-color: rgba(35, 220, 205, 0.78);
}
.custom-tree-node span {
  margin-right: 5px;
}

  .active {
    color: #20a0ff;
  }

  .bim-tree {
    position: absolute;
    top: 54px;
    left: 10px;
  }

  .tree-btn {
    border-radius: 4px;
    background: rgba(0, 0, 0, 0.9);
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .dialog-tree>>>.dy-dialog-body {
    padding: 0px 12px;
  }
</style>