import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '@/views/login'//登录页面
import home from "@/views/home"
  
Vue.use(VueRouter)

//解决路由跳转空值报错
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
        routes: [{
                path: '/',
                redirect: '/login',
            },
            {
                path: '/login',
                name: 'login',
                component: login,
            },
            {
                path: '/home',
                name: 'home',
                component: home,
            },
        ]
    })
    //路由全局守卫 有问题
    // router.beforeEach(async(to, from, next) => {
    //     const hasToken = localStorage.getItem("ioc_token")
    //     if (hasToken) {
    //         next()
    //     } else {
    //         if (to.path == '/login') {
    //             next()
    //         } else {
    //             next({ path: '/login' })
    //         }
    //     }
    // })
    router.beforeEach((to, from, next) => {
        const token = sessionStorage.getItem('gisuser')
        if(to.path!="/login"){
        if (token) {
            console.log(token)
        next()
        } else {
        next({
            path: '/login'
        })
        }
    }
    else{
        next()
    }
    })


export default router